/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["_getInputFromDiv", "checkCondition"] }] */

import ShopifyDriver from './_base';
import { DOM } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractFormValues } = DOM;

// Driver behaviour description:
//     Existing code: yes
//     Remove code: yes
//     Stackable: no
//     Free gift support: no
//     'isValid' implemented: no
//      Codes can be replaced by another code: yes
// sessionTimer 15 min

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		// registered users have another btn selector
		this.APPLY_BUTTON_SELECTOR = '.order-summary__section--discount .field__input-btn.btn';

		this.NEW_URL_FORM_SELECTOR = '.order-summary__section--discount form:first-of-type';
		this.CHECKOUT_EMAIL_PARAM_SELECTOR = '[data-rtl-ensure]';
		this.INPUT_DIV_SELECTOR = '.field--required input, .field--required select';

		this.STEP_SELECTOR = '.step';
	}

	checkCondition() {
		const { href } = window.location;

		const checkoutURL =
			href.startsWith('https://shop.enfamil.com/') &&
			href.includes('/checkouts/') &&
			!href.includes('stock_problems');
		const isTotal =
			document.querySelector(this.TOTAL_SELECTOR) && document.querySelector(this.TOTAL_SELECTOR).offsetParent;
		return checkoutURL && isTotal;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	beforeTestCodes() {
		super.beforeTestCodes();
		this.IS_ON_CONTACT_PAGE = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'contact_information'
			: false;
		this.userIsLoggedIn = document.querySelector('.shopify-checkout-logout');
		// we need to send request to this url only for logged in users
		this.newStringURLCondition = this.userIsLoggedIn && this.IS_ON_CONTACT_PAGE;
		this.newStringURL = this.newStringURLCondition
			? encodeURI(this._newURLForming().slice(0, -1))
			: window.location.href;
	}

	_getCartSummary() {
		return fetch(this.newStringURL, {
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
		}).then(response => response.text());
	}

	_getInputFromDiv(selector) {
		const body = {};
		[...document.querySelectorAll(selector)].reduce((acc, el) => {
			acc[el.name] = el.value;
			return acc;
		}, body);
		return body;
	}

	// this method is used for forming url with query params. This url is used for send request to get tax price(it loads dynamically)
	_newURLForming() {
		const obj = extractFormValues(this.NEW_URL_FORM_SELECTOR);
		const newObj = obj.params;
		// We need to append params in the order, they are written down
		newObj.append('previous_step', 'contact_information');
		newObj.append(
			'checkout[email]',
			document.querySelector(this.CHECKOUT_EMAIL_PARAM_SELECTOR).textContent.slice(1, -1),
		);

		const divElement = this._getInputFromDiv(this.INPUT_DIV_SELECTOR);
		// first we need to add empty params
		Object.keys(divElement).forEach(key => {
			newObj.append(key, '');
		});
		// after that we need to add the same params with values
		Object.entries(divElement).forEach(([key, value]) => {
			newObj.append(key, value);
		});
		newObj.set('checkout[buyer_accepts_marketing]', '0');
		newObj.delete('');
		newObj.delete('checkout[client_details][javascript_enabled]');
		newObj.delete('checkout[client_details][browser_height]');
		newObj.delete('checkout[client_details][browser_width]');
		newObj.delete('checkout[reduction_code]');
		// at the end we need to combine all params in url
		let stringURL = `${window.location.href.split('?')[0]}?`;
		[...newObj.entries()].forEach(([key, value]) => {
			stringURL += `${key}=${value}&`;
		});
		return stringURL;
	}
};

export default {
	name: 'enFamil',
	domain: 'enfamil.com',
	isSinglePageApp: true,
	maxCoupons: 8, // due to test time
	Driver,
};
