import Promise from 'bluebird';
import ShopifyDriver from './_base';
import { DOM } from '../../../utils';

const { extractFromHTML, extractElement } = DOM;

/** Behavior description:
 + Existing code: yes
 + Remove code: yes
 + Stackable: no
 + Free gift support: no
 + Free shipping: no
 + isValid implemented: yes
 + Manually apply code: no
 + Pages, where extension works: Checkout
 + Is driver SPA: yes
 + Gather codes on site pages: no
 + maxCoupons: 4 // because of blocking

 Special case:
 + This merchant has a page with current promotions: https://support.fashionnova.com/hc/en-us/articles/360024552052 .
 But when request on this page is sent, it responds with 304 status code and 'Provisional headers are shown' warning appears.
 Also we can't get it via iframe because 'DENY is one of three possible directives for X-Frame Options'
 Explanation for this warning: https://stackoverflow.com/questions/21177387/caution-provisional-headers-are-shown-in-chrome-debugger
 * */

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.BASE_URL = 'https://www.fashionnova.com/';
		this.SPINNER = '.full-page-overlay';
		this.FORM_SELECTOR = '.order-summary__section--discount form:last-of-type';
		this.loadPageDynamic = true;
		this.APPLIED_CODE_SELECTOR = 'form .reduction-code__text';
		this.MERCHANT_CODES_SELECTOR = '.shipping-banner-carousel .gender-content a';
		this.MERCHANT_CODES_REGEXP = /code:? (\w+)/i;
		this.setFlag = (regex, flag) => new RegExp(regex, flag);

		this.restoreShippingMethod = true;
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCheckout =
			href.startsWith(this.BASE_URL) &&
			href.includes('/checkouts/') &&
			!href.includes('stock_problems') &&
			!href.includes('/thank_you');
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const isSpinner = document.querySelector(this.SPINNER);
		return isOnCheckout && isTotal && !isSpinner;
	}

	getMerchantCodes() {
		return this._getPage().then(responseHTML => {
			return [
				...new Set(
					[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
						.map(elem => elem.innerText.match(this.MERCHANT_CODES_REGEXP))
						.filter(Boolean)
						.map(code => code[1]),
				),
			];
		});
	}

	getExistingCodes() {
		const codes = new Set([...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)].map(el => el.textContent));

		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	beforeCheckCodes(originalPrice) {
		if (!originalPrice) {
			return this._getCartSummary().then(responseHTML => this._parseTotal(responseHTML));
		}
		return originalPrice;
	}

	_getPage(url = this.BASE_URL) {
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_getCartSummary(retry = false) {
		return Promise.delay(2000).then(() => {
			return fetch(window.location.href, {
				method: 'GET',
				credentials: 'include',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
				},
			})
				.then(response => response.text())
				.then(responseHTML => {
					if (!extractFromHTML(responseHTML, this.TOTAL_SELECTOR) && !retry) {
						return this._getCartSummary(true);
					}
					return responseHTML;
				});
		});
	}
};

export default {
	name: 'fashionnova',
	domain: 'fashionnova.com',
	Driver,
	restoreUserData: true,
	isSinglePageApp: true,
	maxCoupons: 4,
};
