import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.FORM_SELECTOR = '.order-summary__section--discount form:first-of-type';
		this.APPLIED_CODE_SELECTOR = '.reduction-code__text';
	}
};

export default {
	name: '1MORE USA',
	domain: 'usa.1more.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return (
			href.startsWith('https://usa.1more.com/') &&
			href.includes('/checkouts/') &&
			!document.querySelector('.product__status--sold-out')
		);
	},
};
