import Promise from 'bluebird';
import ShopifyDriver from './_base';
import bus from '../../bus';
import { DOM, log } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractFormValues, extractFromHTML } = DOM;

// Behavior description:
// we can apply only one code
// We can get existing code
// we can remove code or we can replace old code with new one
// maxCoupons: 5,
//  sessionTimer 15 min

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.contactInfoStep = false;
		this.CONTACT_INFO_FORM_SELECTOR = '.edit_checkout:first-child';
	}

	checkCondition() {
		const isRightUrl = window.location.href.includes('/checkouts/');
		const codeInput = document.querySelector(this.CODES_INPUT_SELECTOR);
		const isCodeInput = codeInput && codeInput.offsetParent;
		const totalEl = document.querySelector(this.TOTAL_SELECTOR);
		const isTotal = totalEl && totalEl.offsetParent;
		const currentState = isRightUrl && isCodeInput && isTotal;
		return currentState;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	beforeTestCodes() {
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		this._getShippingSelectorIndex();
		const step = document.querySelector('.step');
		this.contactInfoStep = step.dataset.step === 'contact_information';
		return Promise.resolve();
	}

	beforeCheckCodes() {
		return this.contactInfoStep
			? this._getCartSummary().then(responseHTML => this._parseTotal(responseHTML))
			: Promise.resolve();
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(responseHTML => (this.loadPageDynamic ? this._getCartSummary(code) : responseHTML))
				.then(responseText => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						isValid: false,
					};

					if (extractFromHTML(responseText, this.APPLIED_CODE_SELECTOR)) {
						result.finalPrice = this._parseTotal(responseText);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.isValid = true;

						return this._removeCode().then(() => bus.$emit('code-checked', result));
					}
					return bus.$emit('code-checked', result);
				}),
		);
	}

	_getCartSummary(code = '') {
		let contactForm = null;
		if (this.contactInfoStep) {
			contactForm = extractFormValues(this.CONTACT_INFO_FORM_SELECTOR).params;
			contactForm.set(this.APPLY_CODE_PARAMETER, code);
		}
		return fetch(`${window.location.href}${contactForm ? '?' : ''}${contactForm ? contactForm.toString() : ''}`, {
			method: 'GET',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
		}).then(response => response.text());
	}
};

export default {
	name: 'Bulletproof',
	domain: 'bulletproof.com',
	isSinglePageApp: true,
	Driver,
	maxCoupons: 5,
};
