import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {};

export default {
	name: 'cotopaxi',
	domain: 'cotopaxi.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://cotopaxi.com/') && href.includes('/checkouts/');
	},
};
