import ShopifyDriver from './_base';

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+
+ Pages, where extension works:
    - checkout
+ Is driver SPA no
+ Gather codes on site pages: no
+ MaxCoupons: 7 (site is slow)
*/

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.FORM_SELECTOR = '.order-summary__section--discount form:first-of-type';
		this.APPLIED_CODE_SELECTOR = '.order-summary__section--discount .reduction-code .reduction-code__text';

		this.SHIPPING_SELECTOR = '.total-line--shipping .total-line__price';

		this.getPureTotal = true;
		this.restoreShippingMethod = true;
	}
};

export default {
	name: 'US Polo Assn',
	domain: 'uspoloassn.com',
	Driver,
	maxCoupons: 7, // site is slow
	restoreUserData: true,
	isCartEmpty() {
		return document.querySelector('.wl-cart-view--empty');
	},
	isOnCartUrl(href) {
		return (
			href.includes('uspoloassn.com') &&
			href.includes('/checkouts/') &&
			!document.querySelector('.product__status--sold-out') &&
			document.querySelector('#checkout_reduction_code')
		);
	},
};
