import ShopifyDriver from './_base';
import { sessionTimer } from '../../lib';
import { format, DOM } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: yes
+ Manually apply code: no
+ Pages, where extension works: Checkout, don't support Shipping page if there is error msg
+ Is driver SPA: yes
+ Gather codes on site pages: yes, from promo page https://www.bhcosmetics.com/pages/offers-promotions
+ maxCoupons: 8, due to `Total test time`
+ sessionTimer - 15 min
*/

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();

		this.APPLIED_CODE_SELECTOR = '.total-line-table .reduction-code__text';
		this.BASE_URL = 'https://www.bhcosmetics.com';
		this.PROMO_PAGE_URL = `${this.BASE_URL}/pages/offers-promotions`;
		this.MERCHANT_CODE_SELECTOR = '.offer__bottom__promo-code';
		// some page elements like shipping or taxes may load dynamically after page loaded
		this.loadPageDynamic = true;
		this.SPINER = '.full-page-overlay';
		this.SHIPPING_ERROR_MSG_SELECTOR = '[data-shipping-warning]';

		this.getPureTotal = true;
		this.restoreShippingMethod = true;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const checkoutURL =
			href.startsWith(this.BASE_URL) && href.includes('/checkouts/') && !href.includes('stock_problems');
		const totalElem = document.querySelector(this.TOTAL_SELECTOR);
		const istotalPrice = totalElem ? elemToFloat(totalElem) !== 0 : false;
		const isSpiner = document.querySelector(this.SPINER);
		const shippingErrorMsg = (document.querySelector(this.SHIPPING_ERROR_MSG_SELECTOR) || {}).offsetParent;

		return checkoutURL && istotalPrice && !isSpiner && !shippingErrorMsg;
	}

	setUserData(userData) {
		// in this case we do request on cart url and ending of id of the element is changed after every request
		const shipppingId = userData.shippingSelector.slice(-30);
		const shipppingSelectorBeginning = userData.shippingSelector.replace(shipppingId, '');
		const checkedShippingOption =
			document.querySelector(`#${userData.shippingSelector}`) ||
			document.querySelector(`[id^=${shipppingSelectorBeginning}]`);

		if (checkedShippingOption) {
			checkedShippingOption.click();
		}
	}

	getMerchantCodes() {
		return this._getPromoPage().then(response =>
			[...extractElement(this.MERCHANT_CODE_SELECTOR, response, true)]
				.filter(el => el.textContent.match(/use code\s?(\w+)/i))
				.map(el => el.textContent.match(/use code\s?(\w+)/i)[1]),
		);
	}

	_getPromoPage() {
		return fetch(this.PROMO_PAGE_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(res => res.text());
	}
};

export default {
	name: 'BH Cosmetics',
	domain: 'bhcosmetics.com',
	isSinglePageApp: true,
	Driver,
	restoreUserData: true,
	maxCoupons: 8, // due to `Total test time`
};
