import ShopifyDriver from './_base';
import { browserUtils, DOM, completionActions, format } from '../../../utils';

const { toFloat } = format;
const { extractFormValues, extractFromHTML, setValue } = DOM;
const { getCookie } = browserUtils;

/*
Driver behaviour description:
	+ Existing code: yes
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: no
	+ Free shipping: no
	+ 'isValid' implemented: yes
	+ Replace code: yes
	+ is SPA: yes
	+ manual apply: yes(first step of checkout)
	+ Restore user data: yes
=== Special case ===
- from time to time site works slow, maxCoupons:6
- we do
 */
const IS_MANUAL_APPLY = document.querySelector('.section--shipping-address');
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.CODE_INPUT_SELECTOR = '#checkout_reduction_code';
		this.APPLY_BUTTON_SELECTOR = '[data-trekkie-id="apply_discount_button"]';
		this.SELECTED_SHIPPING_SELECTOR = '.section--shipping-method input:checked';
		this.ADDRESS_BLOCK = '.section--shipping-address';
		this.SHIPPING_BLOCK = '.section--shipping-method';
		this.BILLING_BLOCK = '.section--payment-method';
		this.TAX_FIELD = '.total-line--taxes';
		this.SELECTED_SHIPPING_ID = null;
		this.restoreShippingMethod = true;
	}

	checkCondition() {
		const { href } = window.location;
		const onRightStep = href.includes('checkouts');

		const isTaxOnShipping =
			document.querySelector(this.ADDRESS_BLOCK) ||
			(document.querySelector(this.SHIPPING_BLOCK) &&
				!document.querySelector(this.TAX_FIELD).classList.contains('hidden')) ||
			document.querySelector(this.BILLING_BLOCK);
		const usSelected = getCookie('_dy_geo')
			? getCookie('_dy_geo')
					.toLowerCase()
					.includes('us.')
			: false;
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);

		return onRightStep && isTaxOnShipping && isTotal && usSelected;
	}

	beforeTestCodes() {
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		if (this.IS_ON_SHIPPING) {
			this.SELECTED_SHIPPING_ID = `#${document
				.querySelector(this.SELECTED_SHIPPING_SELECTOR)
				.getAttribute('id')}`;
		}
		return Promise.resolve();
	}

	beforeCheckCodes() {
		// need to do this, because tax appears after page reload
		return this._getCartSummary().then(resHtml => this._parseTotal(resHtml));
	}

	applyCodes(codes) {
		return IS_MANUAL_APPLY ? this._manuallyApplyCode(codes[0]) : this._applyCode(codes[0]);
	}

	_manuallyApplyCode(code) {
		const applyButton = document.querySelector(this.APPLY_BUTTON_SELECTOR);
		const codesInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		setValue(codesInput, code);
		applyButton.click();
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (this.IS_ON_SHIPPING) {
			totalSumm = data
				? extractFromHTML(data, this.SELECTED_SHIPPING_ID, null, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE)
				: document
						.querySelector(this.SELECTED_SHIPPING_ID)
						.getAttribute(this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE);
		} else {
			totalSumm = data
				? extractFromHTML(data, this.TOTAL_SELECTOR)
				: document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		return toFloat(totalSumm);
	}
};

export default {
	name: 'THINX',
	domain: 'shethinx.com',
	maxCoupons: 6, // site works slow
	completionAction: IS_MANUAL_APPLY ? completionActions.none : completionActions.reload,
	isSinglePageApp: true,
	showDifferentDiscountMessage: true,
	restoreUserData: true,
	Driver,
};
