import ShopifyDriver from './_base';
import { DOM } from '../../../utils';

const { extractFormValues } = DOM;

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.APPLIED_CODE_SELECTOR = '.applied-reduction-code__information';
	}

	beforeTestCodes() {
		super.beforeTestCodes();
		this.bodyData = extractFormValues('.order-summary__section--discount form:first-of-type');
	}
};

export default {
	name: 'MILANI',
	domain: 'milanicosmetics.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.milanicosmetics.com/') && href.includes('/checkouts/');
	},
};
