import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {};

export default {
	name: 'eLuxury',
	domain: 'eluxury.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.eluxury.com/') && href.includes('/checkouts/');
	},
};
