import ShopifyDriver from './_base';
import { format, DOM } from '../../../utils';

const { toFloat } = format;
const { extractFormValues, extractFromHTML } = DOM;

/*
Behavior description:
- can get existing codes;
- can remove codes;
- valid code can be replaced with another valid code;
- can't apply multiple codes (not stackable);
- code testing may be run on 'Checkout' page;
*/

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();

		this.APPLIED_CODE_SELECTOR = '.edit_checkout .reduction-code .reduction-code__text';
		// some page elements like shipping or taxes may load dynamically after page loaded
		this.loadPageDynamic = true;
	}

	beforeTestCodes() {
		this.bodyData = extractFormValues('.order-summary__section--discount form:first-of-type');
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		this._getShippingSelectorIndex();
		return Promise.resolve();
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (this.IS_ON_SHIPPING) {
			const shippingRateSelector = `.content-box__row:nth-of-type(${this.shippingPriceIndex + 1}) .input-radio`;
			totalSumm = data
				? extractFromHTML(data, shippingRateSelector, null, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE)
				: document.querySelector(shippingRateSelector).getAttribute(this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE);
		} else {
			totalSumm = data
				? extractFromHTML(data, this.TOTAL_SELECTOR)
				: document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		// in some case (on shipping step), we can get null, so we try to get the price from page to prevent error
		return toFloat(totalSumm || extractFromHTML(data, this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Measurable Difference',
	domain: 'measurabledifference.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.measurabledifference.com/') && href.includes('/checkouts/');
	},
};
