import ShopifyDriver from './_base';
import { format, DOM, browserUtils, completionActions } from '../../../utils';
import { sessionTimer } from '../../lib';
import backgroundMessaging from '../../lib/background-messaging';

const { toFloat } = format;
const { extractData, extractElement, setValue } = DOM;
const { wait } = browserUtils;

/* Driver behaviour description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: no
     Codes can be replaced by another code: yes
     Manually apply code: yes, on checkout(contact information)
sessionTimer - 15 min
test only 8 codes due to slow site 
======= Special case =======
removed support from the second step of the checkout because of incorrect total
added blocking testing due to blocking user by website if testing code more then 4 times
*/

const DOMAIN = 'bodycandy.com';

const checkoutStep = () => document.querySelector('.step');
const isOnShipping = () => checkoutStep() && checkoutStep().getAttribute('data-step') === 'shipping_method';
const isOnInformation = () => checkoutStep() && checkoutStep().getAttribute('data-step') === 'contact_information';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.BASE_URL = `https://www.${DOMAIN}`;
		this.SUMMARY_SELECTOR = '.order-summary__section--discount';
		this.APPLY_BUTTON_SELECTOR = `${this.SUMMARY_SELECTOR} button`;
		this.REMOVE_DISCOUNT_SELECTOR = '#checkout_clear_discount';
		this.SPINNER_SELECTOR = '.full-page-overlay';
		this.CODES_INPUT_SELECTOR = '.order-summary__section--discount #checkout_reduction_code';
		this.MERCHANT_CODE_SELECTOR = 'table [color="#990033"]';
		this.CURRENT_STEP_SELECTOR = '[aria-current="step"]';
		this.DISABLE_TIME_MS = 20 * 60 * 1000; // 20 minutes
	}

	async checkCondition() {
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 4;
			}
		}
		const { href } = window.location;
		const shippingURL = isOnShipping();
		const spinnerIsPresent = document.querySelector(this.SPINNER_SELECTOR);
		const inputField = document.querySelector(this.CODES_INPUT_SELECTOR);
		const checkoutURL = href.startsWith(this.BASE_URL) && href.includes('/checkouts/');

		return !spinnerIsPresent && checkoutURL && !shippingURL && inputField && isNotMaxCodeTesting;
	}

	getMerchantCodes() {
		return fetch(`${this.BASE_URL}/pages/coupon`, {
			method: 'GET',
		})
			.then(res => res.text())
			.then(html => {
				return [...extractElement(this.MERCHANT_CODE_SELECTOR, html, true)]
					.filter(Boolean)
					.map(elem => elem.textContent);
			});
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	beforeCheckCodes(originalPrice) {
		if (!originalPrice) {
			return this._getCartSummary().then(responseHTML => this._parseTotal(responseHTML));
		}
		return originalPrice;
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
	}

	applyCodes(codes) {
		if (isOnInformation()) {
			return this._applyCodeManually(codes[0]);
		}
		return super.applyCodes(codes);
	}

	_applyCodeManually(code) {
		setValue(document.querySelector(this.CODES_INPUT_SELECTOR), code);
		const isCodeApplied = () => document.querySelector(this.APPLIED_CODE_SELECTOR);
		document.querySelector(this.APPLY_BUTTON_SELECTOR).click();
		return wait(isCodeApplied, 5000);
	}

	_parseTotal(data = false) {
		return toFloat(extractData(this.TOTAL_SELECTOR, data, false));
	}
};

export default {
	name: 'Body Candy',
	domain: 'bodycandy.com',
	Driver,
	completionAction: isOnInformation() ? completionActions.none : completionActions.default,
	maxCoupons: 8, // slow site
	isSinglePageApp: true,
};
