import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, DOM, completionActions, log } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { setValue } = DOM;

// This page work with json
// On the page can be applied normal-discount and special (that's why Set used in getExistingCodes)
// We can't support page `https://shop.globein.com/a/secure/checkout` because of cors requests for code apply
// After reloading page code will be removed, so we can't apply code by request at the end of the testing
// Driver behaviour description:
// -    Existing code: yes
// -    Remove code: yes
// -    Stackable: no
// -    Free gift support: no
// -    'isValid' implemented: yes
// -    Manually apply code: yes
// -    Is driver SPA and add the condition to show popup: no
const DOMAIN = 'globein.com';

const NotTemplateDriver = class Driver extends BaseDriver {
	constructor() {
		super();
		this.REQUEST_HEADERS = null;
		this.XPID_REGEX = new RegExp(/xpid/);
		this.XPID_REGEX_SELECTOR = new RegExp(/xpid:"(.*?)"/);
		this.bodyData = null;
		this.COUNTRY_SELECTOR = '.shipping-form select.country';
		this.APPLIED_CODE_SELECTOR = '.discount-applied .code';
		this.APPLY_BUTTON_SELECTOR = '.submit-discount';
		this.CODE_INPUT_SELECTOR = '.discount-code';
		this.TOTAL_SELECTOR = '.value-total';
		this.REQUEST_URL = `https://${DOMAIN}/subscription/get-price`;
	}

	beforeTestCodes() {
		this.REQUEST_HEADERS = {
			'X-Requested-With': 'XMLHttpRequest',
		};
		const url = new URL(window.location.href);
		this.bodyData = new URLSearchParams({
			giftbox_type: url.searchParams.get('giftbox_type'),
			coupon_code: '',
			country: document.querySelector(this.COUNTRY_SELECTOR).value,
			is_recurrent: url.searchParams.get('is_recurrent'),
		});
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = new Set(
			[...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)]
				.map(codeElement => codeElement.innerText.trim())
				.filter(Boolean),
		);

		return {
			codes: [...codes],
			codeCount: codes.size,
		};
	}

	removeCodes() {
		return this._removeCode().then(responseJSON => this._parseTotal(responseJSON));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);

		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: true,
			};
			return this._applyCode(code).then(responseJSON => {
				if (this._codeAppliedCheck(responseJSON)) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}

				result.finalPrice = this._parseTotal(responseJSON);
				result.finalDiscount = originalPrice - result.finalPrice;

				return bus.$emit('code-checked', result);
			});
		});
	}

	applyCodes(codes) {
		return this._manuallyApplyCode(codes[0]);
	}

	_manuallyApplyCode(code) {
		const applyButton = document.querySelector(this.APPLY_BUTTON_SELECTOR);
		const codesInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		setValue(codesInput, code);
		applyButton.click();
		return Promise.resolve();
	}

	_removeCode() {
		return this._applyCode('');
	}

	_applyCode(code) {
		this.bodyData.set('coupon_code', code);
		return fetch(this.REQUEST_URL, {
			method: 'POST',
			credentials: 'include',
			headers: this.REQUEST_HEADERS,
			body: this.bodyData,
		}).then(response => response.json());
	}

	_codeAppliedCheck(data) {
		return Boolean(data.message);
	}

	_parseTotal(data = false) {
		if (data) {
			return toFloat(data.total) / 100;
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'GlobeIn',
	domain: 'globein.com',
	completionAction: completionActions.none,
	Driver: NotTemplateDriver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.includes('/subscription') && document.querySelector('.discount-code');
	},
};
