import Promise from 'bluebird';
import ShopifyDriver from './_base';
import { sessionTimer } from '../../lib';
import { completionActions, DOM, browserUtils } from '../../../utils';
import backgroundMessaging from '../../lib/background-messaging';

const { setValue, extractElement } = DOM;
const { wait } = browserUtils;

// Driver behaviour description:
//     Existing code: yes
//     Remove code: yes
//     Stackable: no
//     Free gift support: no
//     'isValid' implemented: yes
//      Codes can be replaced by another code: yes
//      manuallyApplyCode: yes
//      page where extention works:  Checkout(information step)
//      maxCodeTesting 3 for 20 minutes;
//      maxCoupons: 8
// sessionTimer - 15 min

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.DOMAIN = 'https://www.mvmtwatches.com';
		this.MERCHANT_CODE_SELECTOR = '.header-nav-top-bar';
		this.MERCHANT_CODE_REGEX = /use code[:]?[\s+]?(.\w+)/i;
		this.getPureTotal = true;
		this.CODE_INPUT_SELECTOR = '#checkout_reduction_code';
		this.CODE_APPLY_SELECTOR = '.field__input-btn';
		this.SHIPPING_EMPTY_SELECTOR = 'button[data-trekkie-id=continue_to_payment_method_button]';
		this.IS_CODE_APPLIED_SELECTOR = '.total-line--reduction';
		this.DISABLE_TIME_MS = 20 * 60 * 1000; // 20 minutes

		sessionTimer.initialize({
			actionInterval: null,
			timeout: 15,
		});
	}

	async checkCondition() {
		const { href } = window.location;

		const checkoutURL =
			href.startsWith(`${this.DOMAIN}/`) && href.includes('/checkouts/') && !href.includes('thank_you');

		const pageIsLoaded = document.readyState === 'complete';

		const toShippingButton = document.querySelector(this.SHIPPING_EMPTY_SELECTOR);

		const isShippingEmpty = toShippingButton ? toShippingButton.disabled : false;

		const isItemOutOfStock = href.includes('stock_problems');

		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(this.DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(this.DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 3;
			}
		}

		return checkoutURL && pageIsLoaded && !isShippingEmpty && !isItemOutOfStock && isNotMaxCodeTesting;
	}

	getMerchantCodes() {
		return fetch(this.DOMAIN)
			.then(res => res.text())
			.then(result =>
				[...extractElement(this.MERCHANT_CODE_SELECTOR, result, true)]
					.map(el => el.textContent.match(this.MERCHANT_CODE_REGEX))
					.filter(Boolean)
					.map(el => el[1]),
			);
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(this.DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(this.DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
	}

	applyCodes(codes) {
		this._manuallyCodeApply(codes[0]);
		return wait(() => document.querySelector(this.IS_CODE_APPLIED_SELECTOR));
	}

	getPreTaxShippingTotal() {
		return Promise.delay(2000).then(() => super.getPreTaxShippingTotal());
	}

	_manuallyCodeApply(code) {
		setValue(document.querySelector(this.CODE_INPUT_SELECTOR), code);
		document.querySelector(this.CODE_APPLY_SELECTOR).click();
	}
};

export default {
	name: 'mvmtwatches',
	domain: 'mvmtwatches.com',
	isSinglePageApp: true,
	completionAction: completionActions.none,
	Driver,
	maxCoupons: 8, // site locks us
};
