import ShopifyDriver from './_base';
import { sessionTimer, backgroundMessaging } from '../../lib';

const DOMAIN = 'https://bestchoiceproducts.com';

/* Driver behaviour description:
    - Existing code: yes
    - Remove code: yes
    - Stackable: no
    - Free gift support: no
    - Free shipping: no
    - Price increase: yes
    - 'isValid' implemented: yes
    - Replace code: yes
====Special cases====
    - sessionTimer - 15 min
    - test only 7 codes, site works slow
    - we may din code testing 2 times per 30 min(due to blicking)
*/

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.TOTAL_ELEMENT_CONFIG = {
			selector: this.TOTAL_SELECTOR,
		};
		this.loadPageDynamic = true;
		this.getPureTotal = true;
		this.restoreShippingMethod = true;
		this.DISABLE_TIME_MS = 30 * 60 * 1000; // 30 minutes
		sessionTimer.initialize();
	}

	async checkCondition() {
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 2;
			}
		}
		const { href } = window.location;
		const isInput = document.querySelector(this.CODES_INPUT_SELECTOR);
		const isOnCheckoutPage =
			href.startsWith(DOMAIN) && href.includes('/checkouts/') && !href.includes('stock_problems');
		return isOnCheckoutPage && isInput && isNotMaxCodeTesting;
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
	}
};

export default {
	name: 'Best Choise Product',
	domain: 'bestchoiceproducts.com',
	Driver,
	maxCoupons: 7, // site works slow
	isSinglePageApp: true,
};
