import Promise from 'bluebird';
import ShopifyDriver from './_base';
import { DOM, sendXhr, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { extractFormValues, extractElement } = DOM;
const { elemToFloat } = format;

// Driver behaviour description:
//     Existing code: yes
//     Remove code: yes
//     Stackable: no
//     Free gift support: no
//     'isValid' implemented: yes
//      Codes can be replaced by another code: yes
// sessionTimer 15 min
// maxCoupons: 4 due to `Total test time`

// On the shipping page, we can not get the original price with the delivery price included.
// Since the delivery block loads later and does not arrive in the response to a request.
// On this site there are no discounts on delivery and the discount is always static
// (regardless of the selected delivery, the amount of discounts does not change), so we will
// take the original price with the delivery, which is chosen by default. The discount will be
// calculated correctly, but the original price will be different from that on the page.

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		// some page elements like shipping or taxes may load dynamically after page loaded
		this.loadPageDynamic = true;

		this.FORM_SELECTOR = '.order-summary__section--discount form:last-child';
		this.SPINNER_SELECTOR = '.section--shipping-method .content-box svg';
		this.INPUT_FIELD_SELECTOR = '#checkout_reduction_code';
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCartUrl = href.startsWith('https://www.activerideshop.com/') && href.includes('/checkouts/');
		const inputCodeField = document.querySelector(this.INPUT_FIELD_SELECTOR);
		const spinnerIsPresent = document.querySelector(this.SPINNER_SELECTOR);
		const currentState = isOnCartUrl && inputCodeField && !spinnerIsPresent;

		return currentState;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	beforeTestCodes() {
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.bodyData.params.delete('checkout[client_details][browser_height]');
		this.bodyData.params.delete('checkout[client_details][browser_width]');
		this.bodyData.params.delete('checkout[client_details][javascript_enabled]');
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		this._getShippingSelectorIndex();
		return Promise.resolve();
	}

	beforeCheckCodes() {
		return this._getCartSummary().then(responseHTML => this._parseTotal(responseHTML));
	}

	_manageCode() {
		return sendXhr(this.bodyData.action, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
				'x-requested-with': 'XMLHttpRequest',
			},
			body: this.bodyData.params,
		}).then(response => response.text());
	}

	_parseTotal(data) {
		return elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
	}
};

export default {
	name: 'Active Ride Shop',
	domain: 'activerideshop.com',
	isSinglePageApp: true,
	Driver,
	maxCoupons: 4, // due to `Total test time`
};
