import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		// code applied dynamically without refreshing page
		this.loadPageDynamic = true;
	}
};

export default {
	name: 'Florists',
	domain: 'florists.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.florists.com/') && href.includes('/checkouts/');
	},
};
