import ShopifyDriver from './_base';
import { DOM } from '../../../utils';

const { extractFormValues, extractFromHTML, extractElement } = DOM;

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.APPLIED_CODE_SELECTOR = '.reduction-code__text';
	}

	beforeTestCodes() {
		super.beforeTestCodes();
		this.bodyData = extractFormValues('.order-summary__section--discount form:first-of-type');
	}

	// on checkout site show total with pounds, method toFloat() work only with $
	_myToFloat(moneyString) {
		if (typeof moneyString === 'number') {
			return moneyString;
		}
		// comma replacement is necessary to not drop things post-comma
		// negation and dollar and pound signs replacement get it to numeric
		const cleanString = moneyString.trim().replace(/[-($|),]/g, '');
		return parseFloat(cleanString);
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (this.IS_ON_SHIPPING) {
			const shippingPriceIndex = this._getShippingSelectorIndex();
			const shippingRateSelector = `.content-box__row:nth-of-type(${shippingPriceIndex + 1}) .input-radio`;
			totalSumm = data
				? extractFromHTML(data, shippingRateSelector, null, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE)
				: document.querySelector(shippingRateSelector).getAttribute(this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE);
		} else {
			totalSumm = data
				? extractElement(this.TOTAL_SELECTOR, data)
				: document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		return this._myToFloat(totalSumm);
	}
};

export default {
	name: 'Lavish Alice',
	domain: 'lavishalice.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://lavishalice.com/') && href.includes('/checkouts/');
	},
};
