import ShopifyDriver from './_base';
import { format, DOM } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat } = format;
/* Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: Checkout
+ Is driver SPA: yes
+ Gather codes on site pages: no
*/
const BASE_URL = 'https://www.bebe.com/';
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.APPLIED_CODE_SELECTOR = '.order-summary__section--discount .reduction-code__text';

		this.restoreShippingMethod = true;
	}

	checkCondition() {
		const { href } = window.location;

		const isOnRightURL =
			href.startsWith(BASE_URL) && href.includes('/checkouts/') && !href.includes('stock_problems');
		const isCodeInput = document.querySelector(this.CODES_INPUT_SELECTOR);

		return isOnRightURL && isCodeInput;
	}

	getPreTaxShippingTotal() {
		return fetch(this.bodyData.action, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
			body: this.bodyData.params,
		})
			.then(response => response.text())
			.then(resText => {
				const total = elemToFloat(extractElement(this.TOTAL_SELECTOR, resText));
				const shipping =
					elemToFloat(extractElement('.total-line--shipping > .total-line__price > span', resText)) || 0;
				const tax = elemToFloat(extractElement('.total-line--taxes > .total-line__price > span', resText)) || 0;
				return total - shipping - tax;
			});
	}
};

export default {
	name: 'Bebe',
	domain: 'bebe.com',
	Driver,
	maxCoupons: 8, // site locks us
	isSinglePageApp: true,
	restoreUserData: true,
};
