import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
	}
};

export default {
	name: 'REN Skincare',
	domain: 'renskincare.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://usa.renskincare.com/') && href.includes('/checkouts/');
	},
};
