import ShopifyDriver from './_base';

/**
Driver behavior:
   Existing code: yes
   Remove code: yes
   Code retesting: yes
   Stackable: no
   Free gift support: no
   isValid implemented: yes
   Show message about free shipping: no
   Driver is SPA: no
   Gather merchant codes: no
   Special case: there may exist yet another checkout page, but I couldn't reach it
 */
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.APPLIED_CODE_SELECTOR = '.reduction-code__text';
	}
};

export default {
	name: 'soylent',
	domain: 'soylent.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.includes('https://soylent.com/') && href.includes('/checkouts/');
	},
};
