import Promise from 'bluebird';
import ShopifyDriver from './_base';
import { DOM, completionActions, browserUtils, format } from '../../../utils';

const { extractData, extractElement, setValue, extractFormValues } = DOM;
const { toFloat } = format;
const { wait } = browserUtils;
/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: yes (valid ones only)
+ Manually apply code: yes, on checkout(contact information)
+
+ Pages, where extension works:
    - Checkout page
+ Is driver SPA yes
	- right url
	- code input is on the page
	- total element is on the page
	- there are no shipping errors
+ Gather codes on site pages: no
+ Special case about the driver works (if it needed).
    - maxCoupons 4 (timing)
	- sessionTimer no
	- we can increase shipping price if code gives bigger discount then shipping price
*/

const checkoutStep = () => document.querySelector('.step');
const isOnShipping = () => checkoutStep() && checkoutStep().getAttribute('data-step') === 'shipping_method';
const isOnInformation = () => checkoutStep() && checkoutStep().getAttribute('data-step') === 'contact_information';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;

		this.SUMMARY_SELECTOR = '.order-summary__section--discount';
		this.FORM_SELECTOR = `${this.SUMMARY_SELECTOR} form`;
		this.APPLY_BUTTON_SELECTOR = `${this.SUMMARY_SELECTOR} button`;
		this.URL_REGEX = /&discount=(\w+)/i;
		this.getPureTotal = true;
		this.APPLIED_CODE_SELECTOR = '.edit_checkout .reduction-code__text';
		this.REMOVE_DISCOUNT_SELECTOR = '#checkout_clear_discount';
		this.SHIPPING_OPRIONS_ELEMENT_SELECTOR = '.content-box.blank-slate p';
		this.SHIPPING_OPTIONS_REGEX = /no shipping methods available/i;
		this.SHIPPING_SECTION_SELECTOR = '.section--shipping-method';
		this.SHIPPING_METHOD_SELECTOR = '.section--shipping-method .input-radio';
	}

	checkCondition() {
		const isRightUrl = window.location.href.includes('/checkouts/');
		const codeInput = document.querySelector(this.CODES_INPUT_SELECTOR);
		const totalEl = document.querySelector(this.TOTAL_SELECTOR);
		const isCodeInput = codeInput && codeInput.offsetParent;
		const isTotal = totalEl && totalEl.offsetParent;
		const isShippingOptions = [...document.querySelectorAll(this.SHIPPING_OPRIONS_ELEMENT_SELECTOR)].some(item =>
			this.SHIPPING_OPTIONS_REGEX.test(item.textContent),
		);

		return isRightUrl && isCodeInput && isTotal && !isShippingOptions;
	}

	checkRestoreDataCondition() {
		return document.querySelector(this.SHIPPING_METHOD_SELECTOR);
	}

	getUserData() {
		if (document.querySelector(this.SHIPPING_SECTION_SELECTOR)) {
			const checkedShippingElement = [...document.querySelectorAll(this.SHIPPING_METHOD_SELECTOR)].find(
				element => element.checked,
			);

			return {
				shippingSelector: `#${checkedShippingElement.getAttribute('id')}`,
			};
		}

		return null;
	}

	setUserData(userData) {
		const checkedShippingOption = document.querySelector(userData.shippingSelector);
		if (checkedShippingOption) {
			checkedShippingOption.click();
		}
	}

	beforeTestCodes() {
		if (window.location.href.includes('discount')) {
			// if url contains discount="SOME_CODE",
			// we can't get right order summary request,
			// but we can fix it, to removes it from URL
			const url = window.location.href.replace(this.URL_REGEX, '');
			window.history.replaceState(window.history.state, window.document.title, url);
		}
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}

		if (isOnShipping()) {
			this.shippingRateSelector = `#${[...document.querySelectorAll(this.ALL_SHIPPINGS_SELECTOR)]
				.find(el => el.checked)
				.getAttribute('id')}`;
		}
		return Promise.resolve();
	}

	removeCodes() {
		if (isOnInformation() && document.querySelector(this.REMOVE_DISCOUNT_SELECTOR)) {
			document.querySelector(this.REMOVE_DISCOUNT_SELECTOR).nextElementSibling.click();
		}
		return super.removeCodes();
	}

	applyCodes(codes) {
		if (isOnInformation()) {
			return this._applyCodeManually(codes[0]);
		}
		return super.applyCodes(codes);
	}

	_applyCodeManually(code) {
		setValue(document.querySelector(this.CODES_INPUT_SELECTOR), code);
		const isCodeApplied = () => document.querySelector(this.APPLIED_CODE_SELECTOR);
		document.querySelector(this.APPLY_BUTTON_SELECTOR).click();
		return wait(isCodeApplied, 5000);
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (isOnShipping()) {
			const shippingRateElement = extractElement(this.shippingRateSelector, data);
			const shippingRateSelector = shippingRateElement
				? this.shippingRateSelector
				: this.SHIPPING_METHOD_SELECTOR;
			totalSumm = extractData(shippingRateSelector, data, false, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE);
		} else {
			totalSumm = extractData(this.TOTAL_SELECTOR, data);
		}
		return toFloat(totalSumm);
	}

	_getCartSummary() {
		// on shipping page shipping methods is loaded dynamically and very slowly so we need to delay before each request
		const promise = document.querySelector('[data-shipping-method-form]') ? Promise.delay(2000) : Promise.resolve();
		return promise
			.then(() =>
				fetch(window.location.href, {
					credentials: 'include',
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
					},
				}),
			)
			.then(response => response.text());
	}
};

export default {
	name: 'Vanity Planet',
	domain: 'vanityplanet.com',
	Driver,
	isSinglePageApp: true,
	restoreUserData: true,
	completionAction: isOnInformation() ? completionActions.none : completionActions.default,
	maxCoupons: 4, // optimal time
	isOnCartUrl() {
		return false;
	},
};
