import Promise from 'bluebird';
import ShopifyDriver from './_base';
import { DOM, log } from '../../../utils';
import { sessionTimer } from '../../lib';
import bus from '../../bus';

const { extractFormValues, extractFromHTML } = DOM;

// Driver behaviour description:
//     Existing code: yes
//     Remove code: yes
//     Stackable: no
//     Free gift support: no
//     'isValid' implemented: yes
//     Codes can be replaced by another code: yes
//     Spa: yes - we can apply codes on each step (on the first step we can apply code only after entering info)
//     session timer - 15min
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.APPLIED_CODE_SELECTOR = '.order-summary__section--discount .reduction-code .reduction-code__text';

		this.loadPageDynamic = true;
		this.STEP_SELECTOR = '.step';
		this.FIELD_SELECTOR = `${this.STEP_SELECTOR} [id^=checkout_]`;

		this.contactInfoStep = false;
		this.CONTACT_INFO_FORM_SELECTOR = '.edit_checkout:first-child';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isContact = document.querySelector(this.STEP_SELECTOR);
		const isFilled = isContact && isContact.dataset.step === 'contact_information' ? this._isFilled() : true;

		const checkoutURL =
			href.startsWith('https://www.outdoorvoices.com') &&
			href.includes('/checkouts/') &&
			!href.includes('stock_problems') &&
			isFilled;

		return checkoutURL && document.readyState === 'complete';
	}

	beforeTestCodes() {
		this.bodyData = extractFormValues(this.FORM_SELECTOR);
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP && this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method';
		this._getShippingSelectorIndex();
		this.contactInfoStep = this.CHECKOUT_STEP.dataset.step === 'contact_information';
		return Promise.resolve();
	}

	beforeCheckCodes() {
		return this.contactInfoStep
			? this._getCartSummary().then(responseHTML => this._parseTotal(responseHTML))
			: Promise.resolve();
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(responseHTML => (this.loadPageDynamic ? this._getCartSummary(code) : responseHTML))
				.then(responseText => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						isValid: false,
					};

					if (extractFromHTML(responseText, this.APPLIED_CODE_SELECTOR)) {
						result.finalPrice = this._parseTotal(responseText);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.isValid = true;
					}
					return this._removeCode().then(() => bus.$emit('code-checked', result));
				}),
		);
	}

	_getCartSummary(code = '') {
		let contactForm = null;
		if (this.contactInfoStep) {
			contactForm = extractFormValues(this.CONTACT_INFO_FORM_SELECTOR).params;
			contactForm.set('step', 'contact_information');
			contactForm.set(this.APPLY_CODE_PARAMETER, code);
		}
		return fetch(`${window.location.href}${contactForm ? '?' : ''}${contactForm ? contactForm.toString() : ''}`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_isFilled() {
		const originalInputLength = [...document.querySelectorAll(this.FIELD_SELECTOR)].filter(
			el => el.offsetParent && !el.id.includes('address2'),
		).length;
		const filledInputLength = [...document.querySelectorAll(this.FIELD_SELECTOR)].filter(
			el => el.offsetParent && !el.id.includes('address2') && el.value !== '',
		).length;
		return originalInputLength === filledInputLength;
	}
};

export default {
	name: 'Outdoor Voices',
	domain: 'outdoorvoices.com',
	isSinglePageApp: true,
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl: () => false,
};
