import ShopifyDriver from './_base';
import { format, DOM } from '../../../utils';

const { elemToFloat, toFloat } = format;
const { extractElement } = DOM;

/** Behavior description:
- existing code: yes
- remove code: yes
- stackable: no
- free gift support: no
- 'isValid' implemented: yes
- replace code: yes
- SPA: yes
- Special case: sometimes website returns page without shipping, in this case discounton the popup can differ from the actual one
	because it's counted manually.
	Also shipping is reset after page reload, therefore, to see the discounted price, restore the shipping manually.
*/

const BASE_URL = 'https://www.rebeccaminkoff.com/';
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.OUT_OF_STOCK_SELECTOR = '.product__status--sold-out';
		this.INPUT_CODE_SELECTOR = '#checkout_reduction_code';
		this.SHIPPING_RADIO_SELECTOR = '.section--shipping-method .input-radio';
		this.SHIPPING_RATE_ATTRIBUTE = 'data-checkout-shipping-rate';
		this.getPureTotal = true;
		window.addEventListener('beforeunload', () => {
			this.reload = true;
		});
		this.capturedShippingPrice = 0;
	}

	checkCondition() {
		const href = window.location.href.toLowerCase();
		const documentLoaded = document.readyState === 'complete';
		const isOnRightUrl = href.startsWith(BASE_URL) && href.includes('/checkouts/');
		const outOfStockElement = document.querySelector(this.OUT_OF_STOCK_SELECTOR);
		const inputCodeElement = document.querySelector(this.INPUT_CODE_SELECTOR);
		return !this.reload && isOnRightUrl && documentLoaded && !outOfStockElement && inputCodeElement;
	}

	beforeCheckCodes() {
		const [selectedShipping] = [...document.querySelectorAll(this.SHIPPING_RADIO_SELECTOR)].filter(
			el => el.checked,
		);
		if (selectedShipping) {
			this.capturedShippingPrice = toFloat(selectedShipping.getAttribute(this.SHIPPING_RATE_ATTRIBUTE));
			if (Number.isNaN(this.capturedShippingPrice)) {
				this.capturedShippingPrice = 0;
			}
		}
	}

	_parseTotal(data) {
		// sometims website does not return page with shipping
		let total = super._parseTotal(data);
		if (!total) {
			const totalWithoutShipping = elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
			total = totalWithoutShipping + this.capturedShippingPrice;
		}
		return total;
	}
};

export default {
	name: 'Rebecca Minkoff',
	domain: 'rebeccaminkoff.com',
	Driver,
	maxCoupons: 8, // site locks us
	isSinglePageApp: true,
};
