import ShopifyDriver from './_base';

const BASE_URL = 'https://store.tonyrobbins.com';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
	}
};

export default {
	name: 'Tony Robbins',
	domain: 'tonyrobbins.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith(BASE_URL) && href.includes('/checkouts/') && !href.includes('stock_problems');
	},
};
