import ShopifyDriver from './_base';

// Driver behaviour description:
//     Existing code: yes
//     Remove code: yes
//     Stackable: no
//     Free gift support: no
//     'isValid' implemented: yes
//      Codes can be replaced by another code: yes

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.APPLIED_CODE_SELECTOR = '.tags-list .reduction-code__text';
	}
};

export default {
	name: 'wildfox',
	domain: 'wildfox.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.wildfox.com/') && href.includes('/checkouts/');
	},
};
