import ShopifyDriver from './_base';
import { DOM, completionActions } from '../../../utils';

const { setValue } = DOM;

/* Driver behaviour
- Free gift support: no
- Free shipping: no
- isValid implemented: yes
- Pages, where extension works: Checkout
- Existing code: yes
- Stackable: no
- manuallyApply: yes
- remove code: no
- SPA: no
* */

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.CODE_INPUT_SELECTOR = '#checkout_reduction_code';
		this.APPLY_CODE_SELECTOR = '.field__input-btn';
	}

	applyCodes(codes) {
		return this._manuallyApplyCode(codes[0]);
	}

	_manuallyApplyCode(code) {
		setValue(document.querySelector(this.CODE_INPUT_SELECTOR), code);
		document.querySelector(this.APPLY_CODE_SELECTOR).click();
	}
};

export default {
	name: 'Windsor',
	domain: 'windsorstore.com',
	showDifferentDiscountMessage: true,
	maxCoupons: 7,
	completionAction: completionActions.none,
	Driver,
	isOnCartUrl(href) {
		return (
			href.startsWith('https://www.windsorstore.com/') &&
			href.includes('/checkouts/') &&
			!document.querySelector('.status-error')
		);
	},
};
