import ShopifyDriver from './_base';
import { sessionTimer } from '../../lib';

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Pages, where extension works: Checkout pages
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ maxCoupons: 10
*/

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.USER_ADDRESS_SELECTOR = '#checkout_shipping_address_first_name';
		this.CODE_INPUT_SELECTOR = '[name="checkout[reduction_code]"]';
		this.STEP_SELECTOR = '.step';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isOnRightURL =
			href.startsWith('https://shop.brayola.com') &&
			href.includes('/checkouts/') &&
			!href.includes('stock_problems');
		const isCodeInputEl = document.querySelector(this.CODE_INPUT_SELECTOR);
		return isOnRightURL && isCodeInputEl;
	}
};

export default {
	name: 'brayola',
	domain: 'brayola.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 8, // site locks us
};
