import Promise from 'bluebird';
import ShopifyDriver from './_base';
import bus from '../../bus';
import { DOM, log } from '../../../utils';

const { extractElement } = DOM;

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			// we need to add this delay, sue to shopify API rate limits
			return Promise.delay(4000).then(() => {
				return this._applyCode(code)
					.then(responseHTML => (this.loadPageDynamic ? this._getCartSummary() : responseHTML))
					.then(responseText => {
						const result = {
							code,
							finalPrice: originalPrice,
							finalDiscount: 0,
							isValid: false,
						};

						if (extractElement(this.APPLIED_CODE_SELECTOR, responseText)) {
							result.finalPrice = this._parseTotal(responseText);
							result.finalDiscount = originalPrice - result.finalPrice;
							result.isValid = true;
						}
						return this._removeCode().then(() => {
							bus.$emit('code-checked', result);
							result.originalPrice = originalPrice;
							return bus.$emit('send-validation-codes', result);
						});
					});
			});
		});
	}

	_getCartSummary() {
		// on shipping page shipping methods is loaded dynamically and very slowly so we need to delay before each request
		const promise = this.IS_ON_SHIPPING ? Promise.delay(500) : Promise.resolve();
		const url = this.IS_ON_SHIPPING
			? `${window.location.href.split('?')[0]}?step=shipping_method`
			: window.location.href;
		return promise
			.then(() =>
				fetch(url, {
					method: 'GET',
					credentials: 'include',
				}),
			)
			.then(response => response.text());
	}
};

export default {
	name: 'radioShack',
	domain: 'radioshack.com',
	Driver,
	validationSupport: true,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.radioshack.com/') && href.includes('/checkouts/');
	},
	// we can`t go to the checkout without any goods
};
