/*    eslint class-methods-use-this: ["error", { "exceptMethods": ["checkCondition"] }] */
import Promise from 'bluebird';
import ShopifyDriver from './_base';
import bus from '../../bus';

import { sessionTimer } from '../../lib';

import { format, DOM, browserUtils, log } from '../../../utils';

const { elemToFloat } = format;
const { extractElement } = DOM;
const { isFirefox } = browserUtils;

/*
Driver behaviour description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: yes
     Codes can be replaced by another code: yes
    Is driver SPA and add the condition to show popup: yes
	Apply codes only after user clicks on the 'Continue to checkout' on the popup
	Run codes is allowed only on payment page
	sessionTimer 15 min
	maxCoupons: 8, // due to low performance
*/

const DOMAIN = 'https://bluemercury.com';
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		// some page elements like shipping or taxes may load dynamically after page loaded
		this.loadPageDynamic = true;
		this.APPLIED_CODE_SELECTOR = '.edit_checkout .reduction-code .reduction-code__text';

		this.TAX_VALUE_SELECTOR = '[data-checkout-total-taxes-target], .total-line--taxes .order-summary__emphasis';
		this.SHIPPING_VALUE_SELECTOR = '.total-line--shipping .total-line__price';

		this.LOGGED_IN_SELECTOR = '.logged-in-customer-information';
		this.INPUT_SELECTOR = '#checkout_reduction_code';
	}

	beforeCheckCodes() {
		this.appliedCode = this.getExistingCodes();
		this.loggedInUser = document.querySelector(this.LOGGED_IN_SELECTOR);
		return Promise.resolve();
	}

	checkCondition() {
		const isInput = document.querySelector(this.INPUT_SELECTOR);
		const isOnCheckoutPage =
			window.location.href.startsWith(DOMAIN) &&
			window.location.href.includes('/checkouts/') &&
			!window.location.href.includes('stock_problems');
		return isOnCheckoutPage && isInput;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(responseHTML => (this.loadPageDynamic ? this._getCartSummary() : responseHTML))
				.then(responseText => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						isValid: false,
					};

					if (extractElement(this.APPLIED_CODE_SELECTOR, responseText)) {
						result.finalPrice = this._parseTotal(responseText);
						result.finalDiscount = originalPrice - result.finalPrice;
						if (isFirefox() && !this.appliedCode.codes.length && this.loggedInUser) {
							result.finalDiscount -= extractElement(this.TAX_VALUE_SELECTOR)
								? elemToFloat(extractElement(this.TAX_VALUE_SELECTOR))
								: 0;
						}
						result.isValid = true;
					}
					return this._removeCode().then(() => bus.$emit('code-checked', result));
				}),
		);
	}

	getPreTaxShippingTotal() {
		return this._getCartSummary().then(responseHTML => {
			const taxValue = elemToFloat(extractElement(this.TAX_VALUE_SELECTOR, responseHTML));
			const isTaxValue = Number.isNaN(taxValue) ? 0 : taxValue;
			const shippingValue = elemToFloat(extractElement(this.SHIPPING_VALUE_SELECTOR, responseHTML));
			const isShippingValue = Number.isNaN(shippingValue) ? 0 : shippingValue;
			const totalValue = this._parseTotal(responseHTML);
			return totalValue - isTaxValue - isShippingValue;
		});
	}
};

export default {
	name: 'Blue Mercury',
	domain: 'bluemercury.com',
	maxCoupons: 8, // due to low performance
	isSinglePageApp: true,
	Driver,
};
