import Promise from 'bluebird';
import ShopifyDriver from './_base';
import bus from '../../bus';
import { format } from '../../../utils';

const { toFloat } = format;

/* Behaviour
 * We have two pages, where we can apply code
 * One page works as base driver `DriverShopifyBase`
 * Another page works as `DriverNotBase`
 */

const DOMAIN = 'entertainment.com';
const IS_CHECKOUT_PAGE = window.location.pathname.includes('/checkouts');
const CODE_INPUT_SELECTORS = '#checkout_reduction_code, .checkout-discount-code';

const DriverShopifyBase = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.FORM_SELECTOR =
			'.order-summary__section--discount form:first-child, .order-summary__section--discount form:last-of-type';
	}
};

const DriverNotBase = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.APPLIED_CODE_SELECTOR = '.total-line__discount_code, .applied-reduction-code__information';
		this.bodyData = null;
		this.APPLY_CODE_PARAMETER = 'discount_code';
		this.APPLY_BUTTON_SELECTOR = '#side_order_summary .apply-discount-code';
		this.CODES_INPUT_SELECTOR = '#side_order_summary .checkout-discount-code';
	}

	beforeTestCodes() {
		this.bodyData = {
			action: `https://account.${DOMAIN}/ajax/get_discount`,
			params: new URLSearchParams(),
		};
		this.bodyData.params.set('products', sessionStorage.ProductInfo);
		if (!this.bodyData.params.has('products')) {
			this.bodyData.params.set('products', sessionStorage.ProductInfo);
		}
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [sessionStorage.getItem('DiscountInfo')].filter(Boolean).map(info => JSON.parse(info).code);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode().then(() => this._calculateTotal());
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
			};
			return (
				this._applyCode(code)
					// here can be response JSON and can be HTML
					.then(response => {
						result.isValid = Boolean(!response.error);
						const discountAmount = response.error ? 0 : response.discount.amount;
						result.finalPrice = originalPrice - discountAmount;
						result.finalDiscount = originalPrice - result.finalPrice;
						return bus.$emit('code-checked', result);
					})
			);
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	_removeCode() {
		return this._applyCode('REMOVE');
	}

	_manageCode() {
		return fetch(this.bodyData.action, {
			method: 'POST',
			credentials: 'include',
			body: this.bodyData.params,
		}).then(response => response.json());
	}

	_calculateTotal() {
		let total = 0;
		const products = JSON.parse(sessionStorage.ProductInfo);
		products.forEach(item => {
			total += toFloat(item.price * item.qty);
		});
		total /= 100;
		const shipping = sessionStorage.getItem('ShippingInfo') ? JSON.parse(sessionStorage.ShippingInfo).price : 0;
		total += shipping;
		return total;
	}
};

export default {
	name: 'Entertainment',
	domain: 'entertainment.com',
	maxCoupons: 8, // site locks us
	get Driver() {
		return IS_CHECKOUT_PAGE ? DriverShopifyBase : DriverNotBase;
	},
	isOnCartUrl(href) {
		return (
			(href.includes('/shop') ||
				href.includes('/checkout-shipping') ||
				href.includes('/checkout-payment') ||
				href.includes('/checkout')) &&
			document.querySelector(CODE_INPUT_SELECTORS)
		);
	},
};
