import Promise from 'bluebird';
import ShopifyDriver from './_base';
import bus from '../../bus';
import { sessionTimer, backgroundMessaging } from '../../lib';
import { DOM, format, log, completionActions } from '../../../utils';

const { toFloat } = format;
const { extractFormValues, extractData, extractElement, setValue } = DOM;

/** Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Pages, where extension works: checkout
+ Is driver SPA yes
+ Gather codes on site pages: yes
+ Special case about the driver works (if it needed).
  - maxCoupons (4) blocking
  - sessionTimer yes (15)
  - showDifferentDiscountMessage: true
  - allow testing, retesting, wait for 20 min to the next testing, because of blocking
 */

const DOMAIN = 'bombas.com';
const DOMAIN_URL = `https://${DOMAIN}/`;

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.MERCHANT_CODES_SELECTOR = '#alertbar';
		this.PROMO_CODES_SELECTOR = '.bee-coupon-dialog';
		this.MERCHANT_CODES_REGEXP = /code\s([\d\w]+)/i;
		this.CODE_INPUT_SELECTORS = '#checkout_reduction_code';
		this.APPLY_BUTTON_SELECTOR = '.field__input-btn';
		this.restoreShippingMethod = true;
		this.DISABLE_TIME_MS = 20 * 60 * 1000; // 20 minutes
		sessionTimer.initialize();
	}

	async checkCondition() {
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 2;
			}
		}
		const isOnCartUrl =
			window.location.href.startsWith(DOMAIN_URL) &&
			window.location.href.includes('/checkouts/') &&
			!window.location.href.includes('stock_problems') &&
			!window.location.href.includes('/thanks-you');
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const currentState = isOnCartUrl && isTotal && isNotMaxCodeTesting;
		return currentState;
	}

	async beforeTestCodes() {
		this.shippingPriceIndex = null;
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
		return Promise.resolve();
	}

	getMerchantCodes() {
		return fetch(DOMAIN_URL)
			.then(response => response.text())
			.then(responseHTML => {
				const mainPageCode = extractElement(this.PROMO_CODES_SELECTOR, responseHTML)
					? [...extractElement(this.PROMO_CODES_SELECTOR, responseHTML, true)]
							.filter(Boolean)
							.map(el => el.getAttribute('data-coupon-code'))
					: [];
				return [
					...new Set(
						[...extractElement(this.MERCHANT_CODES_SELECTOR, responseHTML, true)]
							.map(el => el.textContent.match(this.MERCHANT_CODES_REGEXP))
							.filter(Boolean)
							.map(code => code[1]),
					).add(...mainPageCode),
				];
			});
	}

	removeCodes() {
		return (
			this._removeCode()
				// sometimes after code removing we got in respons old info about total price.
				// So we need to send request to the cart page, and after that  - send request tot get updated total sum
				.then(() => this._getCartSummary())
				.then(responseHTML => (this.IS_ON_SHIPPING ? this._getCartSummary() : responseHTML))
				.then(responseHTML => this._parseTotal(responseHTML))
		);
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			Promise.delay(2500).then(() => {
				this._applyCode(code)
					.then(() => this._getCartSummary())
					.then(responseText => {
						const result = {
							code,
							finalPrice: originalPrice,
							finalDiscount: 0,
							isValid: true,
						};
						if (!responseText || !extractElement(this.APPLIED_CODE_SELECTOR, responseText)) {
							result.isValid = false;
							return bus.$emit('code-checked', result);
						}
						result.finalPrice = this._parseTotal(responseText);
						result.finalDiscount = originalPrice - result.finalPrice;
						return this._removeCode().then(() => bus.$emit('code-checked', result));
					});
			}),
		);
	}

	applyCodes(codes) {
		return this._manuallyApplyCode(codes[0]);
	}

	_manuallyApplyCode(code) {
		setValue(document.querySelector(this.CODE_INPUT_SELECTORS), code);
		document.querySelector(this.APPLY_BUTTON_SELECTOR).click();
		return Promise.resolve();
	}

	_manageCode() {
		return fetch(this.bodyData.action, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
			body: this.bodyData.params,
		}).then(response => (response.status === 409 || response.status === 429 ? null : response.text()));
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (this.IS_ON_SHIPPING) {
			const shippingPriceIndex = this._getShippingSelectorIndex();
			const shippingRateSelector = `.content-box__row:nth-of-type(${shippingPriceIndex + 1}) .input-radio`;
			totalSumm = data
				? extractData(shippingRateSelector, data, false, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE)
				: extractData(this.TOTAL_SELECTOR);
		} else {
			totalSumm = extractData(this.TOTAL_SELECTOR, data);
		}
		return toFloat(totalSumm);
	}
};

export default {
	name: 'Bombas',
	domain: 'bombas.com',
	Driver,
	maxCoupons: 4,
	restoreUserData: true,
	isSinglePageApp: true,
	completionAction: completionActions.none,
	showDifferentDiscountMessage: true,
};
