import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.SOLD_OUT_SELECTOR = '.product__status--sold-out';
		this.SPINNER_SELECTOR = '.section--shipping-method .content-box svg';
	}

	checkCondition() {
		const isRightUrl = window.location.href.includes('/checkouts/');
		const codeInput = document.querySelector(this.CODES_INPUT_SELECTOR);
		const totalEl = document.querySelector(this.TOTAL_SELECTOR);
		const isCodeInput = codeInput && codeInput.offsetParent;
		const isSoldOut = document.querySelector(this.SOLD_OUT_SELECTOR);
		const isTotal = totalEl && totalEl.offsetParent;
		const spinnerIsPresent = document.querySelector(this.SPINNER_SELECTOR);
		const currentState = !spinnerIsPresent && !isSoldOut && isRightUrl && isCodeInput && isTotal;
		return currentState;
	}

	_getCartSummary(tryNumber = 1) {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
		})
			.then(response => response.text())
			.then(
				// it will work only once, on the shipping apge
				responseHTML =>
					!this._parseTotal(responseHTML) && tryNumber < 6
						? this._getCartSummary(tryNumber + 1)
						: responseHTML,
			);
	}
};

export default {
	name: 'TeeFury',
	domain: 'teefury.com',
	isSinglePageApp: true,
	Driver,
	maxCoupons: 8, // site locks us
};
