import ShopifyDriver from './_base';

const BASE_URL = window.location.origin;
/* Behavior description:
 * We can apply only one code
 * We can get and remove existing code
 * We can apply codes only on checkout pages
 */
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
	}
};

export default {
	name: 'Juice Beauty',
	domain: 'juicebeauty.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith(BASE_URL) && href.includes('/checkouts/');
	},
};
