import ShopifyDriver from './_base';

/* Behaviour description:
   - we can apply code
   - we can remove code
   - we can apply only one code
   - we can't replace a valid code with invalid one
   - we can remove code or we can replace old code with new one
*/

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.APPLIED_CODE_SELECTOR = '.reduction-code__text';
	}
};

export default {
	name: 'Cuddl Duds',
	domain: 'cuddlduds.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://cuddlduds.com/') && href.includes('/checkouts/');
	},
};
