import ShopifyDriver from './_base';

/* Driver behaviour description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: yes */

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.APPLIED_CODE_SELECTOR = '.tag__text .reduction-code__text';
	}
};

export default {
	name: 'Lilla P',
	domain: 'lillap.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://lillap.com/') && href.includes('/checkouts/');
	},
};
