import ShopifyDriver from './_base';
import { sessionTimer } from '../../lib';

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Checkout pages
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ Session timer: 15 min
+ MaxCoupons: 10, due to test time
*/

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		// some page elements like shipping or taxes may load dynamically after page loaded
		this.loadPageDynamic = true;
		this.getPureTotal = true;
		this.SOLD_OUT_PRODUCT_SELECTOR = '.product__status--sold-out';
		this.STOCK_PRODUCT_SELECTOR = '.stock-problems-table';
		this.INPUT_FIELD_SELECTOR = '#checkout_reduction_code';
		this.DISCOUNT_REGEX = /\??&?discount=(.*)/;

		this.restoreShippingMethod = true;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;

		return (
			document.readyState === 'complete' &&
			href.startsWith('https://www.stevemadden.com/') &&
			href.includes('/checkouts/') &&
			!document.querySelector(this.SOLD_OUT_PRODUCT_SELECTOR) &&
			!document.querySelector(this.STOCK_PRODUCT_SELECTOR) &&
			document.querySelector(this.INPUT_FIELD_SELECTOR)
		);
	}

	beforeTestCodes() {
		// if apply code on pop-up Cart promo code will be added to the url, need to clean it to get right url
		window.history.pushState(
			window.history.state,
			window.document.title,
			window.location.href.replace(this.DISCOUNT_REGEX, ''),
		);
		return super.beforeTestCodes();
	}
};

export default {
	name: 'Steve Madden',
	domain: 'stevemadden.com',
	Driver,
	restoreUserData: true,
	maxCoupons: 8, // site locks us
	isSinglePageApp: true,
	// no need to write isCartEmpty() because user can`t go to the checkout page without goods
};
