import Promise from 'bluebird';
import ShopifyDriver from './_base';
import { browserUtils } from '../../../utils';
import { extractElement, extractFormValues } from '../../../utils/DOM';
import { backgroundMessaging } from '../../lib';

const { wait } = browserUtils;

/** Behavior description:
    Existing code: yes
    Remove code: yes
    Stackable:  no
    Free gift support:  no
    isValid implemented: yes
	 SPA: yes
====Special cases====
	We reset shipping method on shipping page after code testing.
	Can test only 3 coupons,
	we can test codes 2 times per 30 min
*/

const DOMAIN = 'tableclothsfactory.com';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		// this.FORM_SELECTOR = '.order-summary__section--discount form:last-child, .edit_checkout';
		this.MERACHANT_CODE_SELECTOR = '.announcement-bar-for-mobile';
		this.RESPONSE_SHIPPING_SELECTOR = '[data-shipping-methods] input';
		this.MERCHANT_CODE_REGEX = /code: (\w+)/i;
		// some page elements like shipping or taxes may load dynamically after page loaded
		this.loadPageDynamic = true;
		this.restoreShippingMethod = true;
		this.MAX_TRY_NUMBER = 2;
		this.DISABLE_TIME_MS = 30 * 60 * 1000; // 30 minutes
	}

	async checkCondition() {
		const { href } = window.location;
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 2;
			}
		}
		const isOnFirstStep =
			href.includes('step=contact_information') && document.querySelector('.section--shipping-address');
		const isOnShippingStep = document.querySelector('[data-shipping-method]');
		const isOnPaymentStep = href.includes('step=payment_method');
		const isStockProblem = href.includes('stock_problems') && document.querySelector('[data-payment-subform]');
		const isTotal =
			document.querySelector(this.TOTAL_SELECTOR) && document.querySelector(this.TOTAL_SELECTOR).offsetParent;
		return (
			document.readyState === 'complete' &&
			(isOnFirstStep || isOnShippingStep || isOnPaymentStep) &&
			isTotal &&
			!isStockProblem &&
			isNotMaxCodeTesting
		);
	}

	async beforeTestCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		this._getShippingSelectorIndex();

		return Promise.resolve();
	}

	getMerchantCodes() {
		return fetch(`https://${DOMAIN}`)
			.then(res => res.text())
			.then(resHtml =>
				[...extractElement(this.MERACHANT_CODE_SELECTOR, resHtml, true)]
					.map(item => item.textContent.match(this.MERCHANT_CODE_REGEX))
					.filter(Boolean)
					.map(item => item[1]),
			);
	}

	beforeCheckCodes() {
		return this._getCartSummary().then(responseHTML => this._parseTotal(responseHTML));
	}

	_applyCode(code, tryNumber = 0) {
		const { href } = window.location;
		this.bodyData.params.set(this.APPLY_CODE_PARAMETER, code);
		this.bodyData.params.delete(this.REMOVE_CODE_PARAMETER); // it is important, without it we can`t send right request
		return this._manageCode().then(resHtml => {
			if (
				!extractElement(this.RESPONSE_SHIPPING_SELECTOR, resHtml) &&
				tryNumber < this.MAX_TRY_NUMBER &&
				href.includes('shipping_method') &&
				!href.includes('payment_method')
			) {
				return Promise.delay(2000).then(() => this._applyCode(code, tryNumber + 1));
			}
			return resHtml;
		});
	}

	_manageCode() {
		return fetch(this.bodyData.action, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
			body: this.bodyData.params,
		}).then(response => response.text());
	}

	_getCartSummary() {
		const { href } = window.location;
		const shipSel = () =>
			href.includes('shipping_method') && !href.includes('payment_method')
				? document.querySelector('[data-shipping-method-form]')
				: true;
		return wait(shipSel, 10000, 500)
			.then(() =>
				fetch(window.location.href, {
					credentials: 'include',
					headers: {
						'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
					},
				}),
			)
			.then(response => response.text());
	}
};

export default {
	name: 'Tablecloths Factory',
	domain: 'tableclothsfactory.com',
	maxCoupons: 3, // site works slow
	restoreUserData: true,
	Driver,
	isSinglePageApp: true,
};
