import ShopifyDriver from './_base';
import { DOM, format } from '../../../utils';

const { elemToFloat, toFloat } = format;
const { extractElement, extractData } = DOM;

/** Behavior description:
- existing code: yes
- remove code: yes
- stackable: no
- free gift support: no
- free shipping: no
- 'isValid' implemented: yes
- manually apply code: no
- replace code: there is only one valid code
- Pages, where extension works: Checkout page
- SPA: no
- gather codes on site pages: no
- Special case about the driver works:
  - don't support Cart because there isn't code input;
  - get total price from response
  - if there is free shipping for item - finalPrice in console will be without shipping,
    because after apply code shipping becomes not free and total price will be increase
- credentials (josephhhh@gmail.com, 12345qwerty)
 */

const CODES_INPUT_SELECTOR = '.order-summary__section--discount #checkout_reduction_code';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;

		this.FREE_SHIPPING_RESPONSE_SELECTOR = '.content-box__emphasis';
		this.FREE_SHIPPING_PAGE_SELECTOR = '.total-line--shipping [data-checkout-total-shipping-target]';
	}

	removeCodes() {
		return this._removeCode()
			.then(() => this._getCartSummary())
			.then(responseHTML => {
				// need to check free shipping element
				const freeShippingEl = extractElement(this.FREE_SHIPPING_RESPONSE_SELECTOR, responseHTML);
				this.isFreeShippingInResponse = freeShippingEl
					? freeShippingEl.innerText.toLowerCase().trim() === 'free'
					: false;
				return this._parseTotal(responseHTML);
			});
	}

	beforeCheckCodes(originalPrice) {
		if (!originalPrice) {
			// need to get right total from response
			return this._getCartSummary().then(responseHTML => this._parseTotal(responseHTML));
		}
		return originalPrice;
	}

	_parseTotal(data) {
		let totalSumm = null;
		const isFreeShippingOnPage =
			extractElement(this.FREE_SHIPPING_PAGE_SELECTOR)
				.innerText.toLowerCase()
				.trim() === 'free';
		if (data && this.IS_ON_SHIPPING && !this.isFreeShippingInResponse && !isFreeShippingOnPage) {
			const shippingRateSelector = `.content-box__row:nth-of-type(${this.shippingPriceIndex + 1}) .input-radio`;
			totalSumm = toFloat(extractData(shippingRateSelector, data, false, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE));
		} else {
			totalSumm = elemToFloat(extractElement(this.TOTAL_SELECTOR, data));
		}
		return totalSumm;
	}
};

export default {
	name: 'Wetseal',
	domain: 'wetseal.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return (
			href.startsWith('https://www.wetseal.com/') &&
			href.includes('/checkouts/') &&
			!href.includes('stock_problems') &&
			document.querySelector(CODES_INPUT_SELECTOR)
		);
	},
	// no need to write isCartEmpty() because user can`t go to the checkout page without goods
};
