import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {};

export default {
	name: 'Edens Garden',
	domain: 'edensgarden.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.edensgarden.com/') && href.includes('/checkouts/');
	},
};
