import ShopifyDriver from './_base';
import { sessionTimer } from '../../lib';
import { format, DOM, sendXhr } from '../../../utils';

const { toFloat } = format;
const { extractElement, extractData } = DOM;
/* BEHAVIOUR
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Manually apply code: no
+ Pages, where extension works:
- Cart page, checkout page
 * - Sometimes coupon deletes gift
 * - MaxCoupons = 7, because 1 valid code ~ 8ms
 * - sessionTimer 15 min
 */
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.STEP_SELECTOR = '.step';
		this.INPUT_FIELD_SELECTOR = '.order-summary__sections #checkout_reduction_code';
		this.SHIPPING_ELEMENT_SELECTOR = '.content-box__row .input-radio';
		this.SHIPPING_PRICE_SELECTOR = '.total-line--shipping .order-summary__emphasis';
		this.SHIPPING_PRICE = 0;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCheckoutPage = href.startsWith('https://www.chubbiesshorts.com') && href.includes('/checkouts/');
		const inputField = document.querySelector(this.INPUT_FIELD_SELECTOR);
		const isInput = inputField && inputField.offsetParent;
		return isOnCheckoutPage && isInput;
	}

	checkRestoreDataCondition() {
		const checkouStepElement = document.querySelector(this.STEP_SELECTOR);
		this.IS_ON_SHIPPING = checkouStepElement && checkouStepElement.getAttribute('data-step') === 'shipping_method';
		return this.IS_ON_SHIPPING;
	}

	getUserData() {
		// Nothing to restore on the cart page
		if (this.IS_ON_SHIPPING) {
			const checkedShippingElement = [...document.querySelectorAll(this.SHIPPING_ELEMENT_SELECTOR)].find(
				element => element.checked,
			);
			if (checkedShippingElement) {
				// store shipping price to add it in parseTotal if there will be no shipping options
				this.SHIPPING_PRICE = toFloat(checkedShippingElement.getAttribute('data-checkout-total-shipping')) || 0;
			}
			return checkedShippingElement
				? {
						shippingSelector: `input#${checkedShippingElement.getAttribute('id')}`,
				  }
				: null;
		}
		return null;
	}

	setUserData(userData) {
		if (userData) {
			document.querySelector(userData.shippingSelector).click();
		}
	}

	_getCartSummary() {
		// merchant auto apply code, and change url address
		const { href } = window.location;
		const requestUrl = href.match(/step=/) ? href : href.split('?')[0];
		return sendXhr(requestUrl, {
			method: 'GET',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
		}).then(response => response.text());
	}

	_manageCode() {
		return sendXhr(this.bodyData.action, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
			body: this.bodyData.params,
		}).then(response => response.text());
	}

	_parseTotal(data = false) {
		// there are cases, when shipping options doesn't show
		// we will take total with default shipping
		if (this.IS_ON_SHIPPING) {
			const isShippingMethod = extractElement(this.ALL_SHIPPINGS_SELECTOR, data);
			if (isShippingMethod) {
				const shippingRateSelector = `.content-box__row:nth-of-type(${
					this.shippingPriceIndex < 0 ? 1 : this.shippingPriceIndex + 1
				}) .input-radio`;
				return toFloat(extractData(shippingRateSelector, data, false, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE));
			}
			// we stored shipping price before testing and add here to order total, in case when there are no shipping options in response or on the page
			return toFloat(extractData(this.TOTAL_SELECTOR, data)) + this.SHIPPING_PRICE;
		}
		return toFloat(extractData(this.TOTAL_SELECTOR, data));
	}
};
export default {
	name: 'ChubbiesShorts.com',
	domain: 'chubbiesshorts.com',
	isSinglePageApp: true,
	maxCoupons: 5,
	restoreUserData: true,
	Driver,
};
