import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.FORM_SELECTOR = '.order-summary__section--discount form:first-of-type';
	}
};

export default {
	name: 'Inchbug',
	domain: 'inchbug.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.inchbug.com/') && href.includes('/checkouts/');
	},
	// no need to write isCartEmpty() because user can`t go to the checkout page without goods
};
