import ShopifyDriver from './_base';
import { backgroundMessaging } from '../../lib';
import { browserUtils } from '../../../utils';

const { setCookie, getCookie } = browserUtils;

// Driver behavior description:
// 	+ Existing code: yes
// + Remove code: yes
// + Stackable:  no
// + Free gift support:  no
// + Free shipping:  no
// + isValid implemented: yes
// + Pages, where extension works:
// 	- checkout
// 	+ Is driver SPA: yes
//  + max code testing 4 for 25 minutes
// + Gather codes on site pages: yes(only if user visited main page)

const BASE_URL = 'https://dressbarn.com';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.MERCHANT_CODE_SELECTOR = '.coupon-code';
		this.APPLIED_CODE_SELECTOR = '.reduction-code__text';
		this.loadPageDynamic = true;
		this.restoreShippingMethod = true;
		// eslint-disable-next-line no-useless-escape
		this.APPLIED_CODE_REGEX = /^[\w\-]+/;
		this.DISABLE_TIME_MS = 25 * 60 * 1000;
	}

	async checkCondition() {
		const { href } = window.location;
		const correctUrl = href.startsWith(BASE_URL) && href.includes('/checkouts/');

		if (!getCookie('merchant_code')) {
			const merchantCode =
				document.querySelector(this.MERCHANT_CODE_SELECTOR) &&
				document.querySelector(this.MERCHANT_CODE_SELECTOR).textContent;
			if (merchantCode) {
				await setCookie('merchant_code', merchantCode, 30, 'dressbarn.com');
			}
		}

		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(BASE_URL);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(BASE_URL);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 4;
			}
		}

		return correctUrl && isNotMaxCodeTesting;
	}

	getMerchantCodes() {
		if (getCookie('merchant_code')) {
			return [getCookie('merchant_code')];
		}
		return [];
	}

	getExistingCodes() {
		const codes = [
			...new Set(
				[...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)]
					.map(el => el.textContent.trim().match(this.APPLIED_CODE_REGEX))
					.filter(Boolean)
					.map(el => el[0]),
			),
		];

		return {
			codes,
			codeCount: codes.length,
		};
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(BASE_URL);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(BASE_URL, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
	}
};

export default {
	name: 'Dressbarn',
	domain: 'dressbarn.com',
	restoreUserData: true,
	maxCoupons: 7,
	isSinglePageApp: true,
	Driver,
	// no need to write isCartEmpty() because user can`t go to the checkout page without goods
};
