import ShopifyDriver from './_base';

/*
Driver behavior description:
 Existing code: yes
 Remove code: yes
 Stackable:  no
 Free gift support:  no
 isValid implemented: yes
Special case:
We can run code testing on checkout on
the second step for guest, and on checkout
on the first and on the second step for log in user.
*/
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.isDynamic = false;
	}
};

export default {
	name: 'Volcom',
	domain: 'volcom.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return (
			href.startsWith('https://www.volcom.com/') &&
			href.includes('/checkouts/') &&
			document.querySelector('.product-table') &&
			!href.includes('stock_problems')
		);
	},
};
