import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {};

export default {
	name: 'RuMe Inc',
	domain: 'myrume.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://www.myrume.com/') && href.includes('/checkouts/');
	},
};
