import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
	}
};

export default {
	name: 'The Color Run ',
	domain: 'thecolorrun.com',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://store.thecolorrun.com/') && href.includes('/checkouts/');
	},
};
