import Promise from 'bluebird';
import ShopifyDriver from './_base';
import { DOM, format } from '../../../utils';

const { extractFormValues, extractData } = DOM;
const { toFloat } = format;

/**
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable:  no
+ Free gift support:  no
+ Free shipping: no
+ isValid implemented: yes
+ Pages, where extension works: checkout
+ Is driver SPA yes
+ Gather codes on site pages:  no
 */

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		// code applied dynamically without refreshing page

		this.loadPageDynamic = true;

		this.FORM_SELECTOR = '.order-summary__section--discount form';
		this.currentShippingQuantity = null;
		this.responseShippingQuantity = null;
		this.restoreShippingMethod = true;
	}

	checkCondition() {
		const { href } = window.location;
		const isOnCartUrl = href.startsWith('https://www.morphe.com/') && href.includes('/checkouts/');
		const readyState = document.querySelector('#checkout_reduction_code');
		const completeBtn = document.querySelector('[data-trekkie-id="complete_order_button"]');
		return isOnCartUrl && ((readyState && readyState.offsetParent) || (completeBtn && readyState));
	}

	beforeTestCodes() {
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		if (this.IS_ON_SHIPPING) {
			this.currentShippingQuantity = this._checkShipping();
		}
		return Promise.resolve();
	}

	_checkShipping(data) {
		return extractData('.input-radio', data, true).length;
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (this.IS_ON_SHIPPING) {
			this.responseShippingQuantity = this._checkShipping(data);
			let shippingPriceIndex = this._getShippingSelectorIndex();
			if (this.currentShippingQuantity < this.responseShippingQuantity) {
				shippingPriceIndex += 1;
			} else if (this.currentShippingQuantity > this.responseShippingQuantity) {
				shippingPriceIndex -= 1;
			}
			const shippingRateSelector = `.content-box__row:nth-of-type(${shippingPriceIndex + 1}) .input-radio`;
			totalSumm = data
				? extractData(shippingRateSelector, data, false, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE)
				: extractData(this.TOTAL_SELECTOR);
		} else {
			totalSumm = extractData(this.TOTAL_SELECTOR, data);
		}
		return toFloat(totalSumm);
	}
};

export default {
	name: 'Morphe',
	domain: 'morphe.com',
	isSinglePageApp: true,
	restoreUserData: true,
	maxCoupons: 6, // driver tests codes slowly
	Driver,
};
