import ShopifyDriver from './_base';
import { sessionTimer, backgroundMessaging } from '../../lib';

/*
Behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: yes
+ Codes can be replaced by another code: no
+ Manually apply code: no
+ Pages, where extension works: Checkout (Shipping and Payment)
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ Session timer: 15 min
+ MaxCoupons: 6, due to test time
=====Special case=====
Block popup after 5 times of testing
Popup will not shown on shipping step if it will not shipping method on page
*/

const DOMAIN = 'untuckit.com';

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.TOTAL_ELEMENT_CONFIG = {
			selector: this.TOTAL_SELECTOR,
		};
		this.INFORMATION_STEP_SELECTOR = '[value="contact_information"]';
		this.FORM_SELECTOR = '.order-summary__section--discount form:last-of-type';
		this.BASE_URL = `https://www.${DOMAIN}`;
		this.SPINER = '.full-page-overlay';

		this.DISABLE_TIME_MS = 20 * 60 * 1000; // 20 minutes

		sessionTimer.initialize({
			timeout: 25,
		});
		this.restoreShippingMethod = true;
	}

	async checkCondition() {
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 3;
			}
		}

		const { href } = window.location;
		const isOnCheckout =
			href.startsWith(this.BASE_URL) && href.includes('/checkouts/') && !href.includes('stock_problems');
		const completedOrder = href.includes('/thank_you');
		const isInformationStep = document.querySelector(this.INFORMATION_STEP_SELECTOR);
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const isSpiner = document.querySelector(this.SPINER);
		const isShippingStep = document.querySelector(this.SHIPPING_SECTION_SELECTOR);
		const isChoosingShipping = isShippingStep ? document.querySelector(this.SHIPPING_METHOD_SELECTOR) : true;
		return (
			isOnCheckout &&
			isTotal &&
			!isSpiner &&
			!completedOrder &&
			!isInformationStep &&
			isChoosingShipping &&
			isNotMaxCodeTesting
		);
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
		return Promise.resolve();
	}
};

export default {
	name: 'UNTUCKit',
	domain: 'untuckit.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 6,
	restoreUserData: true,
};
