import ShopifyDriver from './_base';
import { format, DOM } from '../../../utils';
import { sessionTimer } from '../../lib';

const { toFloat } = format;
const { extractFromHTML, extractElement } = DOM;

/** Behavior description:
 * We can apply only one code
 * We can get existing code
 * We can remove existing code
 * sessionTimer 15 min
 * */

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();

		this.TAXES_SELECTOR = '.total-line--taxes span';
		this.LOGGED_IN_USER_SELECTOR = '.logged-in-customer-information';
		this.TAXES = 0;
	}

	checkCondition() {
		const { href } = window.location;

		const checkoutURL =
			href.startsWith('https://www.bellamihair.com/') &&
			href.includes('/checkouts/') &&
			!href.includes('stock_problems');

		return checkoutURL;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODE_SELECTOR)].filter(Boolean).map(el => el.textContent);
		this.APPLIED_CODES_ARRAY_LENGTH = codes.length;
		return {
			codes,
			codeCount: this.APPLIED_CODES_ARRAY_LENGTH,
		};
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (this.IS_ON_SHIPPING) {
			const shippingPriceIndex = this._getShippingSelectorIndex();
			const shippingRateSelector = `.content-box__row:nth-of-type(${shippingPriceIndex + 1}) .input-radio`;
			totalSumm = data
				? extractFromHTML(data, shippingRateSelector, null, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE)
				: document.querySelector(shippingRateSelector).getAttribute(this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE);
		} else {
			this.TAXES = this._getTaxesFromPage(data);
			totalSumm = data
				? extractFromHTML(data, this.TOTAL_SELECTOR)
				: document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		return toFloat(totalSumm) - this.TAXES;
	}

	_getTaxesFromPage(data) {
		if (extractElement(this.LOGGED_IN_USER_SELECTOR, data)) {
			return this.APPLIED_CODES_ARRAY_LENGTH === 0
				? toFloat(
						extractElement(this.TAXES_SELECTOR, data).getAttribute('data-checkout-total-taxes-target') /
							100,
				  )
				: 0;
		}
		return 0;
	}
};

export default {
	name: 'BellamiHair',
	domain: 'bellamihair.com',
	Driver,
	maxCoupons: 8,
	isSinglePageApp: true,
};
