import Promise from 'bluebird';
import ShopifyDriver from './_base';
import backgroundMessaging from '../../lib/background-messaging';
import { DOM, format } from '../../../utils';

const { extractFormValues, extractElement, extractFromHTML } = DOM;
const { toFloat, elemToFloat } = format;

const DOMAIN = 'shop.magnolia.com';
/*
	Driver behavior description:
	+ Existing code: yes
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: no
	+ Free shipping: no
	+ isValid implemented: yes
	+ Codes can be replaced by another code: yes (valid ones only)
	+ Manually apply code: no
	+
	+ Pages, where extension works:
		- heckout steps
	+ Is driver SPA yes
	+ Gather codes on site pages: no
	+ Special case about the driver works (if it needed).
		- we can remove code or we can replace old code with new one
 		- can be run twice on the 1 step and twice on the last step.
*/

const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
		this.contactInfoStep = false;
		this.APPLIED_CODE_SELECTOR = '.order-summary__section--discount .reduction-code__text';
		this.CONTACT_INFO_FORM_SELECTOR = '.edit_checkout:first-child';
		this.TAX_HIDDEN_SELECTOR = '.total-line--taxes.hidden';
		this.TAX_SELECTOR = '.total-line--taxes .total-line__price';
		this.CONTACT_INFO_FORM_SELECTOR = '.edit_checkout:first-child';
		this.isTaxHidden = false;
		this.STEP_SELECTOR = '.step';
		this.step = null;

		this.DISABLE_TIME_MS = 20 * 60 * 1000; // 20 minutes

		this.IS_SOLD_OUT_SELECTOR = '.product__status--sold-out';
		this.SHIPPING_EL_SELECTOR = '.total-line--shipping .order-summary__emphasis';
	}

	async checkCondition() {
		const isRightUrl = window.location.href.includes('/checkouts/');
		const stepElement = document.querySelector(this.STEP_SELECTOR);
		this.step = stepElement && stepElement.dataset.step;
		let isNotMaxCodeTesting = false;
		if (this.step) {
			const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
			if (driverStorage) {
				const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
				if (dataExpired) {
					backgroundMessaging.deleteDriverStorage(DOMAIN);
					isNotMaxCodeTesting = true;
				} else {
					// if there is no info for current step it means that driver hasn't been run on the current page
					isNotMaxCodeTesting = (driverStorage[this.step] || 0) < 2;
				}
			} else {
				isNotMaxCodeTesting = true;
			}
		}
		const isSoldOut = document.querySelector(this.IS_SOLD_OUT_SELECTOR);
		const isRightStep = this.step !== 'shipping_method' && this.step !== 'thank-you';
		const currentState = isRightUrl && !isSoldOut && isRightStep && isNotMaxCodeTesting;

		return currentState;
	}

	beforeTestCodes() {
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		this._getShippingSelectorIndex();
		this.contactInfoStep =
			this.step === 'contact_information' && !document.querySelector(this.SHIPPING_EL_SELECTOR);
		if (this.contactInfoStep) {
			this.isTaxHidden = !!document.querySelector(this.TAX_HIDDEN_SELECTOR);
		}
		return Promise.resolve();
	}

	beforeCheckCodes() {
		// need to send this request to get right total
		return this.contactInfoStep
			? this._applyCode('FAKE_CODE')
					.then(() => this._getCartSummary())
					.then(responseHTML => this._parseTotal(responseHTML))
			: Promise.resolve();
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const currentStorageState = driverStorage || {};
		const currentTestingCount = currentStorageState[this.step] || 0;

		backgroundMessaging.setDriverStorage(DOMAIN, {
			...currentStorageState,
			[this.step]: currentTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});

		return Promise.resolve();
	}

	_getCartSummary() {
		let contactForm = null;
		if (this.contactInfoStep) {
			contactForm = extractFormValues(this.CONTACT_INFO_FORM_SELECTOR).params;
		}
		return fetch(
			`${
				this.contactInfoStep
					? `${window.location.href.split('?')[0]}?${contactForm.toString()}`
					: window.location.href
			}`,
			{
				method: 'GET',
				credentials: 'include',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
				},
			},
		).then(response => response.text());
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (this.contactInfoStep) {
			// if tax is hidden on the page, we will calculate total without taxes from response
			totalSumm = this.isTaxHidden
				? elemToFloat(extractElement(this.TOTAL_SELECTOR, data)) -
				  elemToFloat(extractElement(this.TAX_SELECTOR, data))
				: extractElement(this.TOTAL_SELECTOR, data).textContent;
		} else if (this.IS_ON_SHIPPING) {
			const shippingRateSelector = `.content-box__row:nth-of-type(${this.shippingPriceIndex + 1}) .input-radio`;
			totalSumm = data
				? extractFromHTML(data, shippingRateSelector, null, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE)
				: document.querySelector(shippingRateSelector).getAttribute(this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE);
		} else {
			totalSumm = data
				? extractFromHTML(data, this.TOTAL_SELECTOR)
				: document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		return toFloat(totalSumm);
	}
};

export default {
	name: 'Magnolia',
	domain: 'magnolia.com',
	isSinglePageApp: true,
	maxCoupons: 8, // site locks us
	Driver,
	isOnCartUrl: () => false,
	// no need to write isCartEmpty() because user can`t go to the checkout page without goods
};
