import Promise from 'bluebird';
import bus from '../../bus';
import ShopifyDriver from './_base';
import { DOM, log } from '../../../utils';

const { extractElement } = DOM;

const BASE_URL = 'https://flattummyco.com';
/*
Driver behaviour description:
    Existing code: yes
    Remove code: yes
    Stackable: no
    Free gift support: no
    'isValid' implemented: yes
     Codes can be replaced by another code: yes
    Is driver SPA and add the condition to show popup: no
	maxCoupons: 10, // due to low performance
*/
const Driver = class Driver extends ShopifyDriver {
	constructor() {
		super();
		this.loadPageDynamic = true;
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code)
				.then(responseHTML => (this.loadPageDynamic ? this._getCartSummary() : responseHTML))
				.then(responseText => {
					const result = {
						code,
						finalPrice: originalPrice,
						finalDiscount: 0,
						isValid: false,
					};

					if (extractElement(this.APPLIED_CODE_SELECTOR, responseText)) {
						result.finalPrice = this._parseTotal(responseText);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.isValid = result.finalPrice < originalPrice;
					}
					return this._removeCode().then(() => bus.$emit('code-checked', result));
				}),
		);
	}
};

export default {
	name: 'Flat Tummy Co',
	domain: 'flattummyco.com',
	maxCoupons: 8, // site locks us
	Driver,
	isOnCartUrl: href => href.startsWith(BASE_URL) && href.includes('/checkouts/'),
};
