import Promise from 'bluebird';
import BaseDriver from '../base';
import bus from '../../bus';
import { format, DOM, log } from '../../../utils';
import { extractData } from '../../../utils/DOM';

const { toFloat } = format;
const { extractFormValues, extractFromHTML, extractElement } = DOM;

// Behavior description:
// we can apply only one code
// we can`t replace a valid code with invalid one
// we can remove code or we can replace old code with new one

export default class ShopifyDriver extends BaseDriver {
	constructor() {
		super();

		this.APPLIED_CODE_SELECTOR = '.applied-reduction-code__information, .edit_checkout .reduction-code__text';
		this.TOTAL_SELECTOR = '.payment-due__price';
		this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE = 'data-checkout-total-price';

		// some page elements like shipping or taxes may load dynamically after page loaded
		this.loadPageDynamic = false;

		// form body parameters
		this.APPLY_CODE_PARAMETER = 'checkout[reduction_code]';
		this.REMOVE_CODE_PARAMETER = 'checkout[clear_discount]';

		this.CODES_INPUT_SELECTOR = '.order-summary__section--discount #checkout_reduction_code';

		this.FORM_SELECTOR =
			'.order-summary__section--discount form:first-child, .order-summary__section--discount form';
		this.bodyData = null;

		this.ALL_SHIPPINGS_SELECTOR = '.input-radio';

		this.SHIPPING_SELECTOR = '.total-line--shipping span';
		this.TAX_SELECTOR = '.total-line--taxes span';

		this.SHIPPING_SECTION_SELECTOR = '.section--shipping-method';
		this.SHIPPING_METHOD_SELECTOR = '.section--shipping-method .input-radio';

		this.getPureTotal = false;

		this.restoreShippingMethod = false;
	}

	checkRestoreDataCondition() {
		return this.restoreShippingMethod && document.querySelector(this.SHIPPING_METHOD_SELECTOR);
	}

	// Base Driver methods
	beforeTestCodes() {
		if (!this.bodyData) {
			this.bodyData = extractFormValues(this.FORM_SELECTOR);
		}
		this.CHECKOUT_STEP = document.querySelector('.step');
		this.IS_ON_SHIPPING = this.CHECKOUT_STEP
			? this.CHECKOUT_STEP.getAttribute('data-step') === 'shipping_method'
			: false;
		this._getShippingSelectorIndex();
		return Promise.resolve();
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.APPLIED_CODE_SELECTOR)].filter(Boolean).map(el => el.textContent);

		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode()
			.then(responseHTML => (this.loadPageDynamic ? this._getCartSummary() : responseHTML))
			.then(responseHTML => this._parseTotal(responseHTML));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			// we need to add this delay, sue to shopify API rate limits
			return Promise.delay(4000).then(() => {
				return this._applyCode(code)
					.then(responseHTML => (this.loadPageDynamic ? this._getCartSummary() : responseHTML))
					.then(responseText => {
						const result = {
							code,
							finalPrice: originalPrice,
							finalDiscount: 0,
							isValid: false,
						};

						if (extractElement(this.APPLIED_CODE_SELECTOR, responseText)) {
							result.finalPrice = this._parseTotal(responseText);
							result.finalDiscount = originalPrice - result.finalPrice;
							result.isValid = true;
						}
						return this._removeCode().then(() => bus.$emit('code-checked', result));
					});
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getUserData() {
		if (this._isShippingOptionsElement() && this.restoreShippingMethod) {
			const checkedShippingElement = [...document.querySelectorAll(this.SHIPPING_METHOD_SELECTOR)].find(
				element => element.checked,
			);

			return {
				shippingSelector: checkedShippingElement.getAttribute('id'),
			};
		}

		return null;
	}

	setUserData(userData) {
		const checkedShippingOption = document.querySelector(`#${userData.shippingSelector}`);
		if (checkedShippingOption) {
			checkedShippingOption.click();
		}
	}

	getPreTaxShippingTotal() {
		return this.getPureTotal
			? this._getCartSummary().then(responseHTML => {
					const total = this._parseTotal(responseHTML);
					// we should take shipping from the page because after reloading page will be selected saved shipping
					const shipping = document.querySelector(this.SHIPPING_SELECTOR) || 0;
					// if tax will not be a number - set tax as 0
					const tax = toFloat(extractData(this.TAX_SELECTOR, responseHTML)) || 0;
					let calculatedShipping = 0;
					if (shipping) {
						calculatedShipping =
							shipping.textContent.toLowerCase().includes('free') ||
							shipping.textContent.toLowerCase().includes('calculated at next step')
								? 0
								: shipping.textContent;
					}
					return total - toFloat(calculatedShipping) - tax;
			  })
			: null;
	}

	// Shopify Driver methods
	_applyCode(code) {
		this.bodyData.params.set(this.APPLY_CODE_PARAMETER, code);
		this.bodyData.params.delete(this.REMOVE_CODE_PARAMETER); // it is important, without it we can`t send right request
		return this._manageCode();
	}

	_getShippingSelectorIndex() {
		const allShippings = [...document.querySelectorAll(this.ALL_SHIPPINGS_SELECTOR)];
		const shippingPrices = allShippings.map(shipping => shipping.checked);
		if (!this.shippingPriceIndex) {
			// when we reload page shipping becomes the default position, so we must save position that user choose
			this.shippingPriceIndex = shippingPrices.indexOf(true);
		} else if (this.shippingPriceIndex !== shippingPrices.indexOf(true)) {
			this.shippingPriceIndex = shippingPrices.indexOf(true);
		}
		return this.shippingPriceIndex;
	}

	_isShippingOptionsElement() {
		return document.querySelector(this.SHIPPING_SECTION_SELECTOR);
	}

	_getCartSummary() {
		return fetch(window.location.href, {
			method: 'GET',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
		}).then(response => response.text());
	}

	_manageCode() {
		return fetch(this.bodyData.action, {
			method: 'POST',
			credentials: 'include',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
			},
			body: this.bodyData.params,
		}).then(response => response.text());
	}

	_parseTotal(data) {
		let totalSumm = null;
		if (this.IS_ON_SHIPPING) {
			const shippingRateSelector = `.content-box__row:nth-of-type(${this.shippingPriceIndex + 1}) .input-radio`;
			totalSumm = data
				? extractFromHTML(data, shippingRateSelector, null, this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE)
				: document.querySelector(shippingRateSelector).getAttribute(this.CHECKOUT_TOTAL_SELECTOR_ATTRIBUTE);
		} else {
			totalSumm = data
				? extractFromHTML(data, this.TOTAL_SELECTOR)
				: document.querySelector(this.TOTAL_SELECTOR).textContent;
		}
		return toFloat(totalSumm);
	}

	_removeCode() {
		this.bodyData.params.set(this.REMOVE_CODE_PARAMETER, '1');
		this.bodyData.params.delete(this.APPLY_CODE_PARAMETER);
		return this._manageCode();
	}
}
