import Promise from 'bluebird';
import BaseDriverShopify from './_base';
import BaseDriverRegular from '../base';
import bus from '../../bus';

import { format, log, completionActions, browserUtils, request, requestTypes } from '../../../utils';
import { setValue } from '../../../utils/DOM';
import { sessionTimer } from '../../lib';

const { getCookie } = browserUtils;
const { toFloat, elemToFloat } = format;

/* Behavior description:
+ Existing code: yes
+ Remove code: yes, apply fake code
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ isValid implemented: no
+ Codes can be replaced by another code: yes
+ Manually apply code: yes
+ Pages, where extension works: Checkout and Gift pages, vault.lootcrate.com
+ Is driver SPA: yes
+ Gather codes on site pages: yes(Checkout and Gift pages),  vault.lootcrate.com - no
*/

const isOnGiftPage = window.location.href.includes('gift');
const isOnSecondMerchantVersion = () => window.location.href.includes('cart');
const isOnShopifyDriver = window.location.href.includes('vault.lootcrate.com');
const MERCHANT_CODE_SELECTOR = 'usi_site_coupon';
const API_URL = 'https://api.lootcrate.com';
const checkoutDriver = class Driver extends BaseDriverRegular {
	constructor() {
		super();
		this.TOTAL_PRICE_SELECTOR = '#subscription-today-total';
		this.INPUT_FIELD = '#checkout_coupon_code';
		this.APPLY_BTN_SELECTOR = '#validate-coupon';
		this.APPLY_CODE_BLOCK = '#checkout-show-coupon';
		this.APPLIED_CODE_SELECTOR = '.subscription-coupon-text';
		this.APPLIED_CODE_REGEX = /Coupon: (.*)/i;
		this.CSRF_TOKEN_REGEX = /csrf_token="([^"]*)"/;
		this.SESSION_ID_SELECTOR = 'session_id';
		this.CART_ID_SELECTOR = 'cart_id';
		this.GET_TOKEN_REQUEST_URL = 'https://www.lootcrate.com//session_tokens';

		this.SHIPPING_PAGE_SELECTOR = '[name*=shipping_address]';
		this.BILLING_PAGE_SELECTOR = '#billingInfo';
		this.BILLING_FORM_ACTIVE_SELECTOR = '#newBillingAddress';
		this.COUNTRY_SELECTOR = '.all-countries .selector-content select';
		this.SHIP_COUNTRY_SELECTOR = 'shipping_address_country';
		this.SHIP_CITY_SELECTOR = 'shipping_address_city';
		this.SHIP_STATE_SELECTOR = 'shipping_address_state';
		this.SHIP_ZIP_SELECTOR = 'shipping_address_zip';
		this.SHIP_ADDRESS_1_SELECTOR = 'shipping_address_line_1';
		this.SHIP_ADDRESS_2_SELECTOR = 'shipping_address_line_2';
		this.BILLING_COUNTRY_SELECTOR = 'billing_address_country';
		this.BILLING_ZIP_SELECTOR = 'billing_address_zip';
		this.BILLING_VISITED_SELECTOR = 'billing_page_visited';

		this.FLOW_COUNT = 0;

		request.setType(requestTypes.embedded);
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		if (document.querySelector(this.TOTAL_PRICE_SELECTOR)) {
			// need to set this information in sessionStorage, because need to use it on other page to send request
			if (document.querySelector(this.SHIPPING_PAGE_SELECTOR)) {
				this._setSesionStorageItems(
					this.SHIP_COUNTRY_SELECTOR,
					this.SHIP_CITY_SELECTOR,
					this.SHIP_STATE_SELECTOR,
					this.SHIP_ZIP_SELECTOR,
					this.SHIP_ADDRESS_1_SELECTOR,
					this.SHIP_ADDRESS_2_SELECTOR,
				);
			}
			if (document.querySelector(this.BILLING_FORM_ACTIVE_SELECTOR)) {
				this._setSesionStorageItems(this.BILLING_COUNTRY_SELECTOR, this.BILLING_ZIP_SELECTOR);
			}
			// need to check if user visited billing, because after this need to send shipping information in request
			if (sessionStorage.getItem(this.BILLING_VISITED_SELECTOR) === 'false' || this.FLOW_COUNT < 1) {
				sessionStorage.setItem(
					this.BILLING_VISITED_SELECTOR,
					Boolean(document.querySelector(this.BILLING_PAGE_SELECTOR)),
				);
				this.FLOW_COUNT += 1;
			}
		}
		const { cookieEnabled } = window.navigator;
		const selectedCountry = document.querySelector(this.COUNTRY_SELECTOR);
		const isUS = selectedCountry && selectedCountry.value === 'US';
		const total = (document.querySelector(this.TOTAL_PRICE_SELECTOR) || {}).offsetParent;
		return total && isUS && cookieEnabled;
	}

	beforeTestCodes() {
		[, this.CSRF_TOKEN] = [...document.querySelectorAll('script')]
			.find(element => element.text.match(this.CSRF_TOKEN_REGEX))
			.text.match(this.CSRF_TOKEN_REGEX);

		const shippingInfo = this._getShippingInfo();
		if (isOnSecondMerchantVersion()) {
			this.CART_ID = sessionStorage.getItem(this.CART_ID_SELECTOR);
			this.bodyData = {
				...shippingInfo,
				session_id: sessionStorage.getItem(this.SESSION_ID_SELECTOR),
				id: this.CART_ID,
			};
			return this._setupAccessToken();
		}

		const scripts = [...document.querySelectorAll('script')];
		const options = scripts
			.map(el => el.textContent.match(/gon\.option_values=\[([",0-9]+)/))
			.filter(Boolean)[0][1]
			.replace(/"/g, '');
		const plan = scripts
			.map(el => el.textContent.match(/gon\.plan_name="([a-z0-9-]+)/))
			.filter(Boolean)[0][1]
			.replace(/"/g, '');
		this.bodyData = {
			...shippingInfo,
			addons: [],
			currency: 'USD',
			expand: ['product', 'plan', 'shipping_cycle'],
			plan_name: plan,
			option_values: options,
		};
		return this._setupAccessToken();
	}

	getMerchantCodes() {
		return [getCookie(MERCHANT_CODE_SELECTOR)].filter(Boolean);
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODE_SELECTOR)]
			.map(el => {
				const codeMatch = el.innerText.match(this.APPLIED_CODE_REGEX);
				return codeMatch ? codeMatch[1] : null;
			})
			.filter(el => Boolean(el) && el !== 'null');

		return {
			codes: [...new Set(codes)],
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this._removeCode().then(response => this._parseTotal(response));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
			};
			return this._applyCode(code).then(responseJSON => {
				if (typeof responseJSON === 'string') {
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = this._parseTotal(responseJSON);
				result.finalDiscount = originalPrice - result.finalPrice;
				return this._removeCode().then(() => bus.$emit('code-checked', result));
			});
		});
	}

	applyCodes(codes) {
		if (document.querySelector(this.APPLY_CODE_BLOCK)) {
			document.querySelector(this.APPLY_CODE_BLOCK).click();
		}
		setValue(document.querySelector(this.INPUT_FIELD), codes[0]);
		document.querySelector(this.APPLY_BTN_SELECTOR).click();
	}

	_manageCode(code = '', url) {
		const requestUrl = isOnSecondMerchantVersion()
			? `${API_URL}/v2/shopping_carts/${this.CART_ID}/checkout_preview`
			: `${API_URL}/v1/carts/preview`;
		this.bodyData.coupon_code = code;
		this.requestOptions = {
			headers: {
				accept: 'application / json',
				'content-type': 'application/json',
				authorization: `Bearer ${this.ACCESS_TOKEN}`,
			},
		};
		return request
			.post(url || requestUrl, JSON.stringify(this.bodyData), this.requestOptions)
			.then(response => response.json());
	}

	_applyCode(code) {
		return this._manageCode(code);
	}

	_removeCode() {
		return this._manageCode();
	}

	_getAccessToken() {
		const requestOptions = {
			headers: {
				accept: 'application / json',
				'content-type': 'application/json',
				'x-csrf-token': this.CSRF_TOKEN,
				'x-requested-with': 'XMLHttpRequest',
			},
		};
		return request.post(this.GET_TOKEN_REQUEST_URL, null, requestOptions).then(response => response.json());
	}

	_setupAccessToken() {
		// eslint-disable-next-line no-return-assign
		return this._getAccessToken().then(responseJSON => (this.ACCESS_TOKEN = responseJSON.token));
	}

	_getShippingInfo() {
		// before user visit billing page information isn't setup
		// need to check if user has already visited Billing page
		const isBillingPageVisited = sessionStorage.getItem(this.BILLING_VISITED_SELECTOR) === 'true';
		const shippingInfo = isBillingPageVisited
			? {
					billing_address_country: sessionStorage.getItem(this.BILLING_COUNTRY_SELECTOR)
						? sessionStorage.getItem(this.BILLING_COUNTRY_SELECTOR)
						: sessionStorage.getItem(this.SHIP_COUNTRY_SELECTOR),
					billing_address_zip: sessionStorage.getItem(this.BILLING_ZIP_SELECTOR)
						? sessionStorage.getItem(this.BILLING_ZIP_SELECTOR)
						: sessionStorage.getItem(this.SHIP_ZIP_SELECTOR),
					shipping_address_country: sessionStorage.getItem(this.SHIP_COUNTRY_SELECTOR),
					shipping_address_city: sessionStorage.getItem(this.SHIP_CITY_SELECTOR),
					shipping_address_state: sessionStorage.getItem(this.SHIP_STATE_SELECTOR),
					shipping_address_zip: sessionStorage.getItem(this.SHIP_ZIP_SELECTOR),
					shipping_address_line_1: sessionStorage.getItem(this.SHIP_ADDRESS_1_SELECTOR),
					shipping_address_line_2: sessionStorage.getItem(this.SHIP_ADDRESS_2_SELECTOR),
			  }
			: {};
		return shippingInfo;
	}

	_setSesionStorageItems(...itemSelectors) {
		itemSelectors.forEach(el => sessionStorage.setItem(el, document.querySelector(`[name*=${el}]`).value));
	}

	_parseTotal(response) {
		if (response) {
			if (isOnSecondMerchantVersion()) {
				return toFloat(response.data.total_amount.data.amount);
			}
			return toFloat(response.total_amount.amount);
		}
		return elemToFloat(document.querySelector(this.TOTAL_PRICE_SELECTOR));
	}
};

const giftDriver = class Driver extends BaseDriverRegular {
	constructor() {
		super();
		this.TOTAL_PRICE_SELECTOR = '#order-summary .text-right';
		this.ZIP_CODE_INPUT_SELECTOR = '#checkout_shipping_address_zip';
		this.CODE_INPUT_SELECTOR = '#checkout_coupon_code';
		this.CODE_INPUT_BTN_SELECTOR = '#validate-coupon';
		this.CODE_INPUT_BLOCK = '#collapseFour';
		this.SECTION_PLAN_SELECTOR = '#section-plans div.active';
		this.CODE_VALIDATION_INFO = '.coupon-amount';

		this.COUNTRY_SELECTOR = '.all-countries .selector-content select';
		this.requestOptions = {
			headers: {
				accept: 'application/json',
				'content-type': 'application/json',
			},
		};
		request.setType(requestTypes.embedded);
	}

	checkCondition() {
		const selectedCountry = document.querySelector(this.COUNTRY_SELECTOR);
		const isUS = selectedCountry && selectedCountry.value === 'US';
		const codeInput = document.querySelector(this.CODE_INPUT_BLOCK);
		const codeInputDisplayed = codeInput && codeInput.offsetParent;
		const showPopup = codeInputDisplayed && isOnGiftPage;
		const currentState = codeInputDisplayed && showPopup && isUS;
		return currentState;
	}

	beforeTestCodes() {
		const PLAN_ID = toFloat(
			document
				.querySelector(this.SECTION_PLAN_SELECTOR)
				.getAttribute('id')
				.match(/\d+/)[0],
		);
		const SHIPPING = document.querySelector(this.ZIP_CODE_INPUT_SELECTOR).value;
		this.params = {
			plan_id: PLAN_ID,
			shipping_address_zip: SHIPPING,
		};
		return Promise.resolve();
	}

	getMerchantCodes() {
		return [getCookie(MERCHANT_CODE_SELECTOR)].filter(Boolean);
	}

	getExistingCodes() {
		const codes = [document.querySelector(this.CODE_INPUT_SELECTOR).value].filter(el => el.length);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	beforeCheckCodes() {
		return this._applyCode('REMOVE_CODE').then(response => this._parseTotal(response));
	}

	checkCodes(codes, originalPrice) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code =>
			this._applyCode(code).then(response => {
				const finalPrice = this._parseTotal(response);
				const finalDiscount = originalPrice - finalPrice;
				return bus.$emit('code-checked', { code, finalPrice, finalDiscount });
			}),
		);
	}

	applyCodes(codes) {
		const codeInput = document.querySelector(this.CODE_INPUT_SELECTOR);
		const codeButton = document.querySelector(this.CODE_INPUT_BTN_SELECTOR);
		codeInput.dispatchEvent(new Event('focus'));
		setValue(codeInput, codes[0], 'change');
		codeButton.disabled = false;
		codeButton.click();
		return Promise.resolve();
	}

	_applyCode(code) {
		this.params.coupon_code = code;
		return request
			.post(`${API_URL}/v1/carts/preview`, JSON.stringify(this.params), this.requestOptions)
			.then(response => response.json());
	}

	_parseTotal(response) {
		return toFloat(response.total_amount.amount);
	}
};

const shopifyDriver = class Driver extends BaseDriverShopify {
	constructor() {
		super();
		this.loadPageDynamic = true;
	}

	checkCondition() {
		const { href } = window.location;
		const checkoutURL =
			href.startsWith('https://vault.lootcrate.com') &&
			href.includes('/checkouts/') &&
			!href.includes('stock_problems');
		return checkoutURL;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	beforeTestCodes() {
		super.beforeTestCodes();
		bus.$emit('change-completion-action', completionActions.default);
	}
};

export default {
	name: 'Loot Crate',
	domain: 'lootcrate.com',
	isSinglePageApp: true,
	maxCoupons: isOnShopifyDriver ? 8 : 15,
	completionAction: completionActions.none,
	get Driver() {
		if (isOnShopifyDriver) {
			return shopifyDriver;
		}
		if (isOnGiftPage) {
			return giftDriver;
		}
		return checkoutDriver;
	},
};
