import ShopifyDriver from './_base';

const Driver = class Driver extends ShopifyDriver {};

export default {
	name: 'gammaLabs',
	domain: 'gammalabs.net',
	Driver,
	maxCoupons: 8, // site locks us
	isOnCartUrl(href) {
		return href.startsWith('https://gammalabs.net/') && href.includes('/checkouts/');
	},
};
